<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="productRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="editForm.ProductID" disabled placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <Input v-model="editForm.ProductName_cn" placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品英文名称" prop="ProductName_en">
            <Input v-model="editForm.ProductName_en" placeholder="请输入产品英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称" prop="PanelName_cn">
            <AutoComplete v-model="editForm.PanelName_cn" @on-search="searchName" @on-select="selectName" placeholder="请输入Panel中文名称" style="width:280px">
              <Option v-for="(item,index) in panelNameData" :value="item.PanelName_cn" :key="index">{{item.PanelName_cn}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="Panel版本号" prop="Version">
            <AutoComplete v-model="editForm.Version" :disabled='versionStep' @on-search="searchVersion" @on-select="selectVersion" placeholder="请输入Panel版本号" style="width:280px">
              <Option v-for="(item,index) in panelVersionData" :value="item.Version" :key="index">{{item.Version}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        this.$http.post("/php/product/index.php",qs.stringify({ProductID:this.productID})).then(response=>{
          // console.log(response.data)
          this.editForm = response.data.data[0];
          resolve()
        })
      }).then(()=>{
        this.$http.post("/php/datainterface/getPanelByName.php",qs.stringify({PanelName_cn:this.editForm.PanelName_cn})).then(response=>{
          // console.log(response.data);
          this.panelNameArr = response.data
          this.panelNameData = response.data
          this.$http.post("/php/datainterface/getPanelVsByID.php",qs.stringify({PanelID:this.panelNameData[0].PanelID})).then(response=>{
            // console.log(response.data);
            this.panelVersionArr = response.data
            this.panelVersionData = response.data
          })
        })
      })
    },
    data() {
      return {
        productID:location.search.split("=")[1],
        panelNameArr:[],
        panelNameData:[],
        panelVersionArr:[],
        panelVersionData:[],
        versionStep:true,
        editForm:{},
        rule:{
          PanelName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchName(value){
        this.$http.post("/php/datainterface/getPanelByName.php",qs.stringify({PanelName_cn:value})).then(response=>{
          // console.log(response.data);
          this.panelNameArr = response.data
          this.panelNameData = response.data
          if(this.panelNameData.length===0){
            this.panelVersionArr = []
            this.panelVersionData = []
            this.$Message.warning('panel中文名称不存在，请从下拉框中选择！')
          }
        })
      },
      selectName(value){
        this.editForm.PanelName_cn = value;
        this.panelNameData = this.panelNameArr.filter(item=>{
          return item.PanelName_cn===value
        })
        this.editForm.PanelID = this.panelNameData[0].PanelID
        this.$http.post("/php/datainterface/getPanelVsByID.php",qs.stringify({PanelID:this.editForm.PanelID})).then(response=>{
          // console.log(response.data);
          this.panelVersionArr = response.data
          this.panelVersionData = response.data
          this.versionStep = false;
        })
      },
      searchVersion(value){
        this.panelVersionData = this.panelVersionArr.filter(item=>{
          return item.Version.toUpperCase().indexOf(value.toUpperCase()) !== -1;
        })
        if(this.panelVersionData.length===0 && this.versionStep === false){
          this.editForm.PanelUpdateRecordID='';
          this.$Message.warning('panel版本号不存在，请从下拉框中选择！')
        }
      },
      selectVersion(value){
        this.panelVersionData = this.panelVersionArr.filter(item=>{
          return item.Version === value;
        })
        this.editForm.PanelUpdateRecordID = this.panelVersionData[0].PanelUpdateRecordID
      },
      productRemove(){
        this.$Common.remove(this,"/php/product/del.php",{ProductID:[this.editForm.ProductID]},()=>{
          setTimeout(()=>{
            this.$router.push("/product")
          },1000)
        })
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.panelVersionData.length===0){
              this.editForm.PanelUpdateRecordID='';
              this.$Message.error('保存失败！panel版本号不存在')
              return false;
            }
            let obj={
              ProductID:this.editForm.ProductID,
              PanelUpdateRecordID:this.editForm.PanelUpdateRecordID,
              ProductName_cn:this.editForm.ProductName_cn,
              ProductName_en:this.editForm.ProductName_en
            }
            this.$Common.edit(this,"/php/product/edit.php",obj,()=>{
              this.$router.push("/product")
            })
          }
        })
      },
      backList(){
        this.$router.push("/product")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


