<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的核酸提取</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的核酸提取</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的核酸提取</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="样本编号" prop="BiospeciemensID">
            <Input v-model="addForm.BiospeciemensID" v-if="biospeciemensID" disabled placeholder="请输入样本编号" style="width: 280px"></Input>
            <AutoComplete v-model="addForm.BiospeciemensID" v-if="!biospeciemensID" @on-search="searchBiospeciemensID" @on-select="selectBiospeciemensID" placeholder="请输入样本编号" style="width:280px">
                <Option v-for="(item,index) in biospeciemensData" :value="item.BiospeciemensID" :key="index">{{item.BiospeciemensID}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="核酸公司编号" prop="NucleicAcidSGIID">
            <Input v-model="addForm.NucleicAcidSGIID" placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="NAExtractionDate">
            <DatePicker @on-change="changeNAExtractionDate" show-week-numbers placement="bottom-start" placeholder="请选择核酸提取日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="核酸提取人" prop="NAExtractionManipulatorID">
            <Select v-model="addForm.NAExtractionManipulatorID" placeholder="请输入核酸提取人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="addForm.NAExtractionAmount" placeholder="请输入核酸提取用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="addForm.NAExtractionUnit" placeholder="请输入核酸提取用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="addForm.NAVolume" placeholder="请输入核酸体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="addForm.NAVolumeUnit" placeholder="请输入核酸体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="addForm.NAConc" placeholder="请输入核酸浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="addForm.NAConcUnit" placeholder="请输入核酸浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="addForm.NAAmount" placeholder="请输入核酸总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="addForm.NAAmountUnit" placeholder="请输入核酸总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Select v-model="addForm.NAQuality" placeholder="请选择核酸质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="坏">坏</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="addForm.NAType" placeholder="请输入核酸类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="addForm.Status" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
      })
      if(this.biospeciemensID===undefined){
        let obj = {}
        obj.BiospeciemensID = this.biospeciemensID;
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.biospeciemensArr = response.data.data
          this.biospeciemensData = response.data.data
        })
      }else{
        this.addForm.BiospeciemensID = this.biospeciemensID
      }
    },
    data() {
      return {
        biospeciemensID:location.search.split('=')[1],
        employeeData:[],
        biospeciemensArr:[],
        biospeciemensData:[],
        addForm: {
          BiospeciemensID:'',
          NucleicAcidSGIID:'',
          NAExtractionAmount:'',
          NAExtractionUnit:'',
          NAExtractionManipulatorID:'',
          NAExtractionDate:'',
          NAVolume:'',
          NAVolumeUnit:'',
          NAConc:'',
          NAConcUnit:'',
          NAAmount:'',
          NAAmountUnit:'',
          NAQuality:'',
          NAType:'',
          Status:''
        },
        rule:{
          BiospeciemensID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          NucleicAcidSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          NAExtractionManipulatorID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          NAExtractionDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      changeNAExtractionDate(date){
        this.addForm.NAExtractionDate = date
      },
      searchBiospeciemensID(value){
        let obj = {}
        obj.BiospeciemensID = value
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.biospeciemensArr = response.data.data
          this.biospeciemensData = response.data.data
        })
      },
      selectBiospeciemensID(value){
        this.biospeciemensData = this.biospeciemensArr.filter(item=>{
          return item.BiospeciemensID === value
        })
        this.addForm.BiospeciemensID = value
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            // console.log(obj);
            this.$Common.add(this,"/php/nucleicacids/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        this.$router.push(this.$tore.state.basic.backUrl)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


