import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'

import MolDiagnosis from '@/components/molDiagnosis/MolDiagnosis'
import AddMolDiagnosis from '@/components/molDiagnosis/AddMolDiagnosis'
import BrowMolDiagnosis from '@/components/molDiagnosis/BrowMolDiagnosis'
import EditMolDiagnosis from '@/components/molDiagnosis/EditMolDiagnosis'

import Subject from '@/components/subject/Subject'
import AddSubject from '@/components/subject/AddSubject'
import BrowSubject from '@/components/subject/BrowSubject'
import AddDiagnosis from '@/components/subject/AddDiagnosis'
import EditDiagnosis from '@/components/subject/EditDiagnosis'
import EditSubject from '@/components/subject/EditSubject'

import Biospeciemens from '@/components/biospeciemens/Biospeciemens'
import AddBiospeciemens from '@/components/biospeciemens/AddBiospeciemens'
import BrowBiospeciemens from '@/components/biospeciemens/BrowBiospeciemens'
import EditBiospeciemens from '@/components/biospeciemens/EditBiospeciemens'

import NucleicAcid from '@/components/nucleicAcid/NucleicAcid'
import AddNucleicAcid from '@/components/nucleicAcid/AddNucleicAcid'
import BrowNucleicAcid from '@/components/nucleicAcid/BrowNucleicAcid'
import EditNucleicAcid from '@/components/nucleicAcid/EditNucleicAcid'

import Library from '@/components/library/Library'
import AddLibrary from '@/components/library/AddLibrary'
import BrowLibrary from '@/components/library/BrowLibrary'
import EditLibrary from '@/components/library/EditLibrary'

import AddQC from '@/components/QC/AddQC'
import EditQC from '@/components/QC/EditQC'

import AddVariant from '@/components/variant/AddVariant'
import EditVariant from '@/components/variant/EditVariant'

import Panel from '@/components/panel/Panel'
import AddPanel from '@/components/panel/AddPanel'
import BrowPanel from '@/components/panel/BrowPanel'
import EditPanel from '@/components/panel/EditPanel'

import AddPanelRec from '@/components/panel/AddPanelRec'
import BrowPanelRec from '@/components/panel/BrowPanelRec'
import EditPanelRec from '@/components/panel/EditPanelRec'
import AddTeamNumber from '@/components/panel/AddTeamNumber'
import AddGene from '@/components/panel/AddGene'

import Product from '@/components/product/Product'
import AddProduct from '@/components/product/AddProduct'
import BrowProduct from '@/components/product/BrowProduct'
import EditProduct from '@/components/product/EditProduct'

import AddProductRec from '@/components/product/AddProductRec'
import BrowProductRec from '@/components/product/BrowProductRec'
import EditProductRec from '@/components/product/EditProductRec'
import AddTeamNumber2 from '@/components/product/AddTeamNumber'
import AddGene2 from '@/components/product/AddGene'

import Pipeline from '@/components/pipeline/Pipeline'
import AddPipeline from '@/components/pipeline/AddPipeline'
import BrowPipeline from '@/components/pipeline/BrowPipeline'
import EditPipeline from '@/components/pipeline/EditPipeline'

import AddPipelineRec from '@/components/pipeline/AddPipelineRec'
import BrowPipelineRec from '@/components/pipeline/BrowPipelineRec'
import EditPipelineRec from '@/components/pipeline/EditPipelineRec'
import AddTeamNumber3 from '@/components/pipeline/AddTeamNumber'
import AddGene3 from '@/components/pipeline/AddGene'

import User from '@/components/userInfo/User'
import AddUser from '@/components/userInfo/AddUser'
import EditUser from '@/components/userInfo/EditUser'
import ChangePwd from '@/components/userInfo/ChangePwd'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      component: Home,
      redirect: '/moldiagnosis',
      children: [
        {
          path: 'moldiagnosis',
          component: MolDiagnosis
        },
        {
          path: 'addmoldiagnosis',
          component: AddMolDiagnosis
        },
        {
          path: 'browmoldiagnosis',
          component: BrowMolDiagnosis
        },
        {
          path: 'editmoldiagnosis',
          component: EditMolDiagnosis
        },
        {
          path: 'subject',
          component: Subject
        },
        {
          path: 'addsubject',
          component: AddSubject
        },
        {
          path: 'browsubject',
          component: BrowSubject
        },
        {
          path: 'editsubject',
          component: EditSubject
        },
        {
          path: 'adddiagnosis',
          component: AddDiagnosis
        },
        {
          path: 'browdiagnosis',
          component: EditDiagnosis
        },
        {
          path: 'editdiagnosis',
          component: EditDiagnosis
        },
        {
          path: 'biospeciemens',
          component: Biospeciemens
        },
        {
          path: 'addbiospeciemens',
          component: AddBiospeciemens
        },
        {
          path: 'browbiospeciemens',
          component: BrowBiospeciemens
        },
        {
          path: 'editbiospeciemens',
          component: EditBiospeciemens
        },
        {
          path: 'nucleicacid',
          component: NucleicAcid
        },
        {
          path: 'addnucleicacid',
          component: AddNucleicAcid
        },
        {
          path: 'brownucleicacid',
          component: BrowNucleicAcid
        },
        {
          path: 'editnucleicacid',
          component: EditNucleicAcid
        },
        {
          path: 'library',
          component: Library
        },
        {
          path: 'addlibrary',
          component: AddLibrary
        },
        {
          path: 'browlibrary',
          component: BrowLibrary
        },
        {
          path: 'editlibrary',
          component: EditLibrary
        },
        {
          path: 'addqc',
          component: AddQC
        },
        {
          path: 'browqc',
          component: EditQC
        },
        {
          path: 'editqc',
          component: EditQC
        },
        {
          path: 'addvariant',
          component: AddVariant
        },
        {
          path: 'browvariant',
          component: EditVariant
        },
        {
          path: 'editvariant',
          component: EditVariant
        },
        {
          path: 'panel',
          component: Panel
        },
        {
          path: 'addpanel',
          component: AddPanel
        },
        {
          path: 'browpanel',
          component: BrowPanel
        },
        {
          path: 'editpanel',
          component: EditPanel
        },
        {
          path: 'addpanelrec',
          component: AddPanelRec
        },
        {
          path: 'browpanelrec',
          component: BrowPanelRec
        },
        {
          path: 'editpanelrec',
          component: EditPanelRec
        },
        {
          path: 'addteamnumber',
          component: AddTeamNumber
        },
        {
          path: 'addgene',
          component: AddGene
        },
        {
          path: 'product',
          component: Product
        },
        {
          path: 'addproduct',
          component: AddProduct
        },
        {
          path: 'browproduct',
          component: BrowProduct
        },
        {
          path: 'editproduct',
          component: EditProduct
        },
        {
          path: 'addproductrec',
          component: AddProductRec
        },
        {
          path: 'browproductrec',
          component: BrowProductRec
        },
        {
          path: 'editproductrec',
          component: EditProductRec
        },
        {
          path: 'addteamnumber2',
          component: AddTeamNumber2
        },
        {
          path: 'addgene2',
          component: AddGene2
        },
        {
          path: 'pipeline',
          component: Pipeline
        },
        {
          path: 'addpipeline',
          component: AddPipeline
        },
        {
          path: 'browpipeline',
          component: BrowPipeline
        },
        {
          path: 'editpipeline',
          component: EditPipeline
        },
        {
          path: 'addpipelinerec',
          component: AddPipelineRec
        },
        {
          path: 'browpipelinerec',
          component: BrowPipelineRec
        },
        {
          path: 'editpipelinerec',
          component: EditPipelineRec
        },
        {
          path: 'addteamnumber3',
          component: AddTeamNumber3
        },
        {
          path: 'addgene3',
          component: AddGene3
        },
        {
          path: 'user',
          component: User
        },
        {
          path: 'adduser',
          component: AddUser
        },
        {
          path: 'browuser',
          component: EditUser
        },
        {
          path: 'edituser',
          component: EditUser
        },
        {
          path: 'changepwd',
          component: ChangePwd
        }
      ]
    }
  ]
})



