<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/biospeciemens">样本管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="bioRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子诊断编号" prop="MolDiagnosisID">
            <Input v-model="editForm.MolDiagnosisID" placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="病理编号" prop="PathologicNumber">
            <Input v-model="editForm.PathologicNumber" placeholder="请输入病理编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本原始编号" prop="SampleOriID">
            <Input v-model="editForm.SampleOriID" placeholder="请输入样本原始编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本公司编号" prop="SampleSGIID">
            <Input v-model="editForm.SampleSGIID" placeholder="请输入样本公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本类型" prop="SampleType">
            <Select v-model="editForm.SampleType" placeholder="请选择样本类型" style="width: 280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
            </Select>
          </FormItem>
          <FormItem label="取样部位" prop="Site">
            <Input v-model="editForm.Site" placeholder="请输入取样部位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样日期" prop="SamplingDate">
            <DatePicker type="date" @on-change="changeSamplingDate" :value="editForm.SamplingDate" show-week-numbers placement="bottom-start" placeholder="请选择取样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期" prop="ReceivedDate">
            <DatePicker type="date" @on-change="changeReceivedDate" :value="editForm.ReceivedDate" show-week-numbers placement="bottom-start" placeholder="请选择收样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
            <Select v-model="editForm.RecipientsID" placeholder="请选择收样人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
            <Select v-model="editForm.IsTumorCell" placeholder="请选择是否为肿瘤细胞" style="width: 280px">
              <Option value="是">是</Option>
              <Option value="否">否</Option>
            </Select>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="editForm.TumorCellContent" placeholder="请输入肿瘤细胞含量（数字）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本状态">
            <Select v-model="editForm.Status" placeholder="请选择状态" style="width:280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="editForm.Quantity" placeholder="请输入样本量（数字）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量单位">
            <Input v-model="editForm.Unit" placeholder="请输入样本量单位" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
        // this.reviewerData = response.data
      })
      this.$Common.list(this,"/php/biospecimens/index.php",{},(response)=>{
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        employeeData:[],
        editForm:{},
        rule:{
          MolDiagnosisID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PathologicNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleOriID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleType:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Site:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SamplingDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            // {
            //   validator: validateSamplingDate, trigger: 'blur,change'
            // }
          ],
          ReceivedDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            // {
            //   validator: validateReceivedDate, trigger: 'blur,change'
            // }
          ]
        }
      }
    },
    methods: {
      bioRemove(){
        this.$Common.remove(this,"/php/biospecimens/del.php",{BiospeciemensID:[this.biospeciemensID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })
      },
      changeSamplingDate(date){
        this.editForm.SamplingDate = date
      },
      changeReceivedDate(date){
        this.editForm.ReceivedDate = date
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/biospecimens/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


