<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="productRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <DatePicker v-model="browForm.CreateDate" readonly show-week-numbers placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="browForm.LatestUpdateDate" readonly show-week-numbers placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="browForm.ProductID"  readonly placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="browForm.ProductName_cn" readonly placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品英文名称">
            <Input v-model="browForm.ProductName_en" readonly placeholder="请输入产品英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称">
            <Input v-model="browForm.PanelName_cn" readonly placeholder="请输入Panel中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号">
            <Input v-model="browForm.Version" readonly placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addProductRec">添加新产品升级记录</Button>
          </div>
          <div class="listcontainer">
            <Table stripe border :loading="loading" height="200" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/product/index.php",qs.stringify({ProductID:this.productID})).then(response=>{
        // console.log(response.data);
        this.browForm = response.data.data[0];
      })
      //获取更新记录列表
      this.handlePage(this.pageNum)
    },
    data() {
      return {
        productID:location.search.split("=")[1],
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"产品更新记录编号",
            key:'ProductUpdateRecordID',
            align: 'center'
          },
          {
            title:"产品版本号",
            key:'Version',
            align: 'center'
          },
          {
            title:"产品状态",
            key:'ProductStatus',
            align: 'center'
          },
          {
            title:"产品上线日期",
            key:'ProductOnlineDate',
            align: 'center'
          },
          {
            title:"产品下线日期",
            key:'ProductOfflineDate',
            align: 'center'
          },
          {
            title:"变异类型",
            key:'VariantType',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ProductUpdateRecordID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ProductUpdateRecordID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ProductUpdateRecordID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      productRemove(){
        this.$Common.remove(this,"/php/product/del.php",{ProductID:[this.browForm.ProductID]},()=>{
          setTimeout(()=>{
            this.$router.push("/product")
          },1000)
        })
      },
      handleEdit(){
        this.$router.push("/editproduct?productid=" + this.productID)
      },
      backList(){
        this.$router.push("/product")
      },
      addProductRec(){
        this.$router.push("/addproductrec?productid=" + this.productID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browproductrec?productrecid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editproductrec?producrectid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/product/productrecord/del.php",{ProductUpdateRecordID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ProductUpdaterecordID)
        })
        this.$Common.batchRemove(this,IDS,"/php/product/productrecord/del.php",{ProductUpdateRecordID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading=true;
        this.pageNum = val;
        this.$http.post("/php/product/productrecord/index.php",qs.stringify({
          ProductID:this.productID,
          pageNum:this.pageNum,
          pageSize:this.pageSize,
          })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data;
        this.TotalNum = Number(response.data.num);
        this.loading = false;
      })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


