<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="libRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="140">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="browForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="browForm.LibID" disabled placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸编号" prop="NucleicAcidID">
            <Input v-model="browForm.NucleicAcidID" placeholder="请输入核酸编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="LibSGIID">
            <Input v-model="browForm.LibSGIID" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建人"  prop="LibConsManipulator">
            <Input v-model="browForm.LibConsManipulator" placeholder="请输入文库构建人" style="width: 280px"></Input>
          </FormItem>
          </FormItem>
          <FormItem label="文库构建日期" prop="LibConsDate">
            <DatePicker type="date" :value="browForm.LibConsDate"  placement="bottom-start" placeholder="请选择文库构建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="browForm.LibConsAmount" placeholder="请输入文库构建用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量单位">
            <Input v-model="browForm.LibConsUnit" placeholder="请输入文库构建用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积">
              <Input v-model="browForm.LibVolume" placeholder="请输入文库体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积单位">
              <Input v-model="browForm.LibVolumeUnit" placeholder="请输入文库体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度">
              <Input v-model="browForm.LibConc" placeholder="请选择文库浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度单位">
              <Input v-model="browForm.LibConcUnit" placeholder="请选择文库浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量">
            <Input v-model="browForm.LibAmount" placeholder="请输入文库总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量单位">
            <Input v-model="browForm.LibAmountUnit" placeholder="请输入文库总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小">
            <Input v-model="browForm.LibInsSize" placeholder="请输入文库插入片段大小" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小单位">
            <Input v-model="browForm.LibInsSizeUnit" placeholder="请输入文库插入片段大小单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库质量">
            <Input v-model="browForm.LibQuality" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="browForm.Status" readonly style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="qcCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addQC">添加新QC结果</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="qcLoading" ref="selection" :columns="qcTableHead" :data="qcTableData" @on-selection-change="qcHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn" @click="batchQCRemove">移除选中的{{qcMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="qcTotalNum" :current="qcPageNum" :page-size="qcPageSize" show-elevator placement="top" size="small" @on-change="qcHandlePage" @on-page-size-change='qcHandlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="variantCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addVariant">添加新变异</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="variantLoading" ref="selection" :columns="variantTableHead" :data="variantTableData" @on-selection-change="variantHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn" @click="batchVariantRemove">移除选中的{{variantMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="variantTotalNum" :current="variantPageNum" :page-size="variantPageSize" show-elevator placement="top" size="small" @on-change="variantHandlePage" @on-page-size-change='variantHandlePageSize'></Page>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        this.$Common.list(this,"/php/library/edit.php",{LibID:this.libID},(response)=>{
          this.browForm = response.data.data[0]
          resolve()
        })
      }).then(()=>{
        //获取QC列表
        this.qcHandlePage(this.qcpageNum)
        //获取变异列表
        this.variantHandlePage(this.variantPageNum)
      })
    },
    data() {
      return {
        libID:location.search.split("=")[1],
        browForm:{},
        qcLoading: true,
        qcTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"总碱基数",
            key:'TotalBases',
            align: 'center'
          },
          {
            title:"总序列数量",
            key:'TotalReadNumber',
            align: 'center'
          },
          {
            title:"重复率",
            key:'DuplicationRate',
            align: 'center'
          },
          {
            title:"对比率",
            key:'MappingRate',
            align: 'center'
          },
          {
            title:"捕获效率",
            key:'CaptureEfficacy',
            align: 'center'
          },
          {
            title:"探针平均深度",
            key:'ProbeMeanDepth',
            align: 'center'
          },
          {
            title:"探针中点深度",
            key:'ProbeMedianDepth',
            align: 'center'
          },
          {
            title:"探针均一性",
            key:'ProbeUniformity',
            align: 'center'
          },
          {
            title:"覆盖率",
            key:'DepthGT1Rate',
            align: 'center'
          },
          {
            title:"覆盖率≥30",
            key:'DepthGT30Rate',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcShow(params.row.QCID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcEdit(params.row.QCID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcRemove(params.row.QCID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        qcTableData:[],
        qcTotalNum:0,
        qcMultipleSelection:[],
        qcPageNum:1,
        qcPageSize:10,
        
        variantLoading: true,
        variantTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Gene Symbol",
            key:'Symbol',
            align: 'center'
          },
          {
            title:"染色体名称及位置",
            key:'ChrName_Position',
            align: 'center'
          },
          {
            title:'转录本',
            key:'transcript',
            align:'center'
          },
          {
            title:"变异类型",
            key:'VariantType',
            align: 'center'
          },
          {
            title:"基因型",
            key:'Genotype',
            align: 'center'
          },
          {
            title:"基因频率",
            key:'MinorAlleleFraction',
            align: 'center'
          },
          {
            title:"exon/intron 及位置",
            key:'exon_intron_Location',
            align: 'center'
          },
          {
            title:"HGVSp",
            key:'HGVSp',
            align: 'center'
          },
          {
            title:"HGVSc",
            key:'HGVSc',
            align: 'center'
          },
          {
            title:"临床意义",
            key:'Clin_Sig',
            align: 'center'
          },
          {
            title:"标记",
            key:'Flag',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantShow(params.row.VariantID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantEdit(params.row.VariantID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantRemove(params.row.VariantID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        variantTableData:[],
        variantTotalNum:0,
        variantMultipleSelection:[],
        variantPageNum:1,
        variantPageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editlibrary?libid=" + this.libID)
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
      libRemove(){
        this.$Common.remove(this,"/php/library/del.php",{LibID:[this.libID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })
      },
      addQC(){
        //将当前受检者的ID传入
        this.$router.push("/addqc?libid=" + this.libID)
      },
      qcShow (id) {
        // console.log(id);
        this.$router.push("/browqc?qcid=" + id)
      },
      qcEdit (id) {
        // console.log(id);
        this.$router.push("/editqc?qcid=" + id)
      },
      qcRemove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/qc/del.php",{QCID:[id]},()=>{
          this.qcHandlePage(this.qcPageNum)
          this.qcMultipleSelection = []
        })
      },
      batchQCRemove(){
        let IDS = []
        this.qcMultipleSelection.forEach(item=>{
          IDS.push(item.QCID)
        })
        this.$Common.remove(this,"/php/qc/del.php",{QCID:IDS},()=>{
          this.qcHandlePage(this.qcPageNum)
          this.qcMultipleSelection = []
        })
      },
      qcHandleSelectionChange(val){
        this.qcMultipleSelection = val;
      },
      qcHandlePage(val){
        this.qcLoading = true;
        this.qcPageNum = val;
        let obj = {
          LibID:this.libID,
          pageNum:this.qcPageNum,
          pageSize:this.qcPageSize
        }
        this.$Common.list(this,"/php/qc/edit.php",obj,(response)=>{
          this.qcTableData = response.data.data
          this.qcTotalNum = Number(response.data.num)
          this.qcLoading = false;
        })
      },
      qcHandlePageSize(val){
        this.qcPageSize = val;
        this.qcpageNum = 1
        this.qcHandlePage(this.qcpageNum)
      },
      addVariant(){
        //将当前受检者的ID传入
        this.$router.push("/addvariant?libid=" + this.libID)
      },
      variantShow (id) {
        // console.log(id);
        this.$router.push("/browvariant?variantid=" + id)
      },
      variantEdit (id) {
        // console.log(id);
        this.$router.push("/editvariant?variantid=" + id)
      },
      variantRemove(id){
        // console.log(id); 
        this.$Common.remove(this,"/php/snp_indel/del.php",{VariantID:[id]},()=>{
          this.variantHandlePage(this.variantPageNum)
          this.variantMultipleSelection = []
        }) 
      },
      batchVariantRemove(){
        let IDS = []
        this.variantMultipleSelection.forEach(item=>{
          IDS.push(item.VariantID)
        })
        this.$Common.remove(this,"/php/snp_indel/del.php",{VariantID:IDS},()=>{
          this.variantHandlePage(this.variantPageNum)
          this.variantMultipleSelection = []
        })
      },
      variantHandleSelectionChange(val){
        this.variantMultipleSelection = val;
      },
      variantHandlePage(val){
        this.variantLoading = true;
        this.variantPageNum = val;
        let obj = {
          LibID:this.libID,
          pageNum:this.variantPageNum,
          pageSize:this.variantPageSize
        }
        this.$Common.list(this,"/php/snp_indel/edit.php",obj,(response)=>{
          this.variantTableData = response.data.data
          this.variantTableData.forEach(item=>{
            item.ChrName_Position = item.ChrName + '&' + item.Position
            item.exon_intron_Location = item.exon_intron + '&' + item.Location
          })
          this.variantTotalNum = Number(response.data.num)
          this.variantLoading = false;
        })
      },
      variantHandlePageSize(val){
        this.variantPageSize = val;
        this.variantPageNum = 1;
        this.variantHandlePage(this.variantPageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .qcCon{
      box-sizing:border-box;
      border-bottom:1px dashed #ccc;
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


