<template>
  <div class="home">
    <Layout>
      <Header>
        <Col span="6">
          <img src="/static/imgs/logo_ky.png" alt="鹍远基因">
        </Col>
        <Col span="12">
          <h1>基础信息管理系统</h1>
        </Col>
        <Col span="6" class="userInfo">
          <div class="avatar">
            <span @mouseenter="exitIcon" @mouseleave="userIcon" @click="showModal">
              <Avatar icon="person" size="large" />
            </span>
            <span class="username">root</span>
          </div>
        </Col>
        <Modal v-model="showExitModal" @on-ok="loginOut" width="300">
          <p style="text-align:center;font-size:16px">确认要退出吗？</p>
        </Modal>
      </Header>
      <Layout>
        <Sider hide-trigger>
          <Menu ref="sideMenu" :active-name="activeName" theme="light" :open-names="openSide" align="left">
            <div id="basicDataSider">
              <MenuItem name="1-1">
                <div @click="navLink('moldiagnosis')">
                  <img src="../../static/imgs/moldiagnosisIcon.png" alt="detection" class="icon">
                  <span>分子诊断管理</span>
                </div>
              </MenuItem>
              <MenuItem name="2-1">
                <div @click="navLink('subject')">
                  <img src="../../static/imgs/subjectIcon.png" alt="detection" class="icon">
                  <span>受检者管理</span>
                </div>
              </MenuItem>
              <MenuItem name="3-1">
                <div @click="navLink('biospeciemens')">
                  <img src="../../static/imgs/cellIcon.png" alt="detection" class="icon">
                  <span>样本管理</span>
                </div>
              </MenuItem>
              <MenuItem name="4-1">
                <div @click="navLink('nucleicacid')">
                  <img src="../../static/imgs/chromoIcon.png" alt="detection" class="icon">
                  <span>核酸管理</span>
                </div>
              </MenuItem>
              <MenuItem name="5-1">
                <div @click="navLink('library')">
                  <img src="../../static/imgs/libIcon.png" alt="detection" class="icon">
                  <span>文库管理</span>
                </div>
              </MenuItem>
              <Submenu name="6">
                <template slot="title">
                  <img src="../../static/imgs/productIcon.png" alt="detection" class="icon">
                    产品管理
                </template>
                <MenuItem name="6-1">
                  <span @click="navLink('panel')">Panel 管理</span>
                </MenuItem>
                <MenuItem name="6-2">
                  <span @click="navLink('product')">Product 管理</span>
                </MenuItem>
                <MenuItem name="6-3">
                  <span @click="navLink('pipeline')">Pipeline 管理</span>
                </MenuItem>
              </Submenu>
              <MenuItem name="7-1">
                <div @click="navLink('user')">
                  <img src="../../static/imgs/userIcon.png" alt="detection" class="icon">
                  <span>用户管理</span>
                </div>
              </MenuItem>
            </div>
          </Menu>
        </Sider>
        <Layout :style="{padding: '0 24px'}">
          <transition name="component-fade" mode="out-in">
            <!--<keep-alive>
              <router-view></router-view>
            </keep-alive>-->
            <router-view></router-view>
          </transition>
          <Footer>Singlera Genomic Inc | Copyright 2018</Footer>
        </Layout>
      </Layout>
    </Layout>
  </div>
  
</template>

<script>
export default {
  mounted() {
    // console.log(window.innerHeight);
    let asideHeight = window.innerHeight - 80 + 'px'
    $('.ivu-layout-sider').css('minHeight', asideHeight)
    // console.log(currentPath);
    //判断当前网址，让对应的侧边栏高亮，防止用户刷新页面后侧边栏高亮效果消失
    this.highSider()
  },
  data() {
    return {
      currentPath:location.pathname.split("/")[1],
      activeName:"",
      openSide:[],
      showExitModal: false
    }
  },
  methods: {
    highSider(){
      switch (this.currentPath){
        case "moldiagnosis":
          this.activeName = "1-1";
        break;
        case "addmoldiagnosis":
          this.activeName = "1-1";
        break;
        case "browmoldiagnosis":
          this.activeName = "1-1";
        break;
        case "editmoldiagnosis":
          this.activeName = "1-1";
        break;
        case "subject":
          this.activeName = "2-1";
        break;
        case "addsubject":
          this.activeName = "2-1";
        break;
        case "browsubject":
          this.activeName = "2-1";
        break;
        case "editsubject":
          this.activeName = "2-1";
        break;
        case "adddiagnosis":
          this.activeName = "2-1";
        break;
        case "browdiagnosis":
          this.activeName = "2-1";
        break;
        case "editdiagnosis":
          this.activeName = "2-1";
        break;
        case "biospeciemens":
          this.activeName = "3-1";
        break;
        case "addbiospeciemens":
          this.activeName = "3-1";
        break;
        case "browbiospeciemens":
          this.activeName = "3-1";
        break;
        case "editbiospeciemens":
          this.activeName = "3-1";
        break;
        case "nucleicacid":
          this.activeName = "4-1";
        break;
        case "addnucleicacid":
          this.activeName = "4-1";
        break;
        case "brownucleicacid":
          this.activeName = "4-1";
        break;
        case "editnucleicacid":
          this.activeName = "4-1";
        break;
        case "library":
          this.activeName = "5-1";
        break;
        case "addlibrary":
          this.activeName = "5-1";
        break;
        case "browlibrary":
          this.activeName = "5-1";
        break;
        case "editlibrary":
          this.activeName = "5-1";
        break;
        case "addqc":
          this.activeName = "5-1";
        break;
        case "editqc":
          this.activeName = "5-1";
        break;
        case "addvariant":
          this.activeName = "5-1";
        break;
        case "editvariant":
          this.activeName = "5-1";
        break;
        case "panel":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addpanel":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browpanel":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editpanel":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addpanelrec":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browpanelrec":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editpanelrec":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addteamnumber":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addgene":
          this.openSide = ["6"];
          this.activeName = "6-1";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "product":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addproduct":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browproduct":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editproduct":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addproductrec":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browproductrec":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editproductrec":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addteamnumber2":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addgene2":
          this.openSide = ["6"];
          this.activeName = "6-2";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "pipeline":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addpipeline":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browpipeline":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editpipeline":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addpipelinerec":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "browpipelinerec":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "editpipelinerec":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addteamnumber3":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "addgene3":
          this.openSide = ["6"];
          this.activeName = "6-3";
          this.$nextTick(function() {
            this.$refs.sideMenu.updateOpened();
            this.$refs.sideMenu.updateActiveName();
          })
        break;
        case "user":
          this.activeName = "7-1";
        break;
        case "adduser":
          this.activeName = "7-1";
        break;
        case "browuser":
          this.activeName = "7-1";
        break;
        case "edituser":
          this.activeName = "7-1";
        break;
        case "changepwd":
          this.activeName = "7-1";
        break;
      }
    },
    //头像移入改变icon
    exitIcon() {
      $('.avatar span>i').addClass('ivu-icon-power')
    },
    //头像移出还原icon
    userIcon() {
      $('.avatar span>i').removeClass('ivu-icon-power')
    },
    showModal() {
      this.showExitModal = true
    },
    loginOut() {
      this.$router.push('/login')
    },
    navLink(path){
      this.$router.push('/'+path)
    }
  },
  watch:{
    $route(to,from){
      this.currentPath = this.$route.path.split('/')[1];
      this.highSider()
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/themeColor.less';
.home {
  .ivu-layout-header {
    height: 80px;
    line-height: 0; // 解决模态框点击确认后改变高度的问题
    border-bottom: 1px solid @themeColor;
    text-align: center;
    background: @bgc;
    .ivu-col {
      line-height: 80px;
      img {
        width: 220px;
        height: 60px;
        margin: 10px 0 0 10px;
      }
      .ivu-avatar {
        background-color: @themeColor;
      }
      .username {
        font-size: 20px;
      }
    }
    .userInfo {
      .ivu-btn{
        margin-right:50px;
        &:hover{
          border-color:@themeColor;
          color:@themeColor;
        }
      }
      .avatar {
        height: 80px;
        overflow: hidden;
      }
    }
  }
  .ivu-layout-sider {
    background-color: @themeColor;
    .ivu-menu-vertical .ivu-menu-item, 
    .ivu-menu-vertical .ivu-menu-submenu-title{
      height:50px;
      line-height:50px;
      padding:0px;
      >div{
        padding-left:25px;
        span{
          display:inline-block;
          width:80%;
        }
      }
    }
  }
  .ivu-menu-light {
    color: #fff;
    background-color: @themeColor;
    width:201px !important;
  }
  .ivu-menu-item {
    font-size:14px;
    color: #fff;
  }
  .ivu-menu-submenu{
    font-size:14px;
    .ivu-menu-item {
      text-align:center;
      font-size:12px;
      span{
        display:inline-block;
        width:100%;
      }
    }
  }
  #basicDataSider,
  #chemoTherapySider,
  #TargetedTherapySider{
    margin-top:50px;
  }
  .icon{
    width: 15px;
    height: 15px;
    margin-right:5px;
    vertical-align: middle;
    display:inline-block;
  }
  .ivu-layout-footer {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding:0;
    font-size: 12px;
    color: #666;
    text-align: center;
  }
}
</style>



