<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="subjectRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly  style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检者姓名" prop="Name">
            <Input v-model="browForm.Name" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="出生日期">
            <DatePicker v-model="browForm.DateOfBirth" readonly style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="性别">
            <Input v-model="browForm.Gender" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证件类型">
            <Input v-model="browForm.IDType" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证件号码">
            <Input v-model="browForm.IDNumber" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="手机号码">
            <Input v-model="browForm.PhoneNumber" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="邮箱地址">
            <Input v-model="browForm.Email" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="家族史">
            <Input v-model="browForm.familyHistory" readonly type="textarea" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="diagnosisCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addDiagnosis">添加新临床诊疗</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="loading" ref="selection" :columns="diagnosisTableHead" :data="diagnosisTableData" @on-selection-change="diagHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn" @click="diagBatchRemove">移除选中的{{diagMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="diagnosisTotalNum" :current="diagPageNum" :page-size="diagPageSize" show-elevator placement="top" size="small" @on-change="diagHandlePage" @on-page-size-change='diagHandlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="molDiagnosisCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addMolDiagnosis">添加新分子诊断</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="molLoading" ref="selection" :columns="molDiagnosisTableHead" :data="molDiagnosisTableData" @on-selection-change="molDiagHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="molDiagnosisTotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="molDiagHandlePage" @on-page-size-change='molDiagHandlePageSize'></Page>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      //获取受检者详情
      this.$Common.list(this,"/php/subject/index.php",{SubjectID:this.subjectID},(response)=>{
        this.browForm = response.data.data[0]
        if(this.browForm.Gender==='m'){
          this.browForm.Gender='男'
        }else if(this.browForm.Gender==='f'){
          this.browForm.Gender='女'
        }
      })
      // 获取临床诊疗数据
      this.diagHandlePage(this.diagPageNum)
      // 获取分子诊断数据
      this.molDiagHandlePage(this.pageNum)
    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
        browForm:{
          Name:'',
          DateOfBirth:'',
          Gender:'',
          IDType:'',
          IDNumber:'',
          PhoneNumber:'',
          Efront:'',
          Eback:'@163.com',
          Email:'',
          FamilyHistory:''
        },
        rule:{
          SubjectName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        },
        loading: true,
        molLoading: true,
        diagnosisTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"临床诊疗编号",
            key:'DiagnosisID',
            align: 'center'
          },
          {
            title:"临床诊断描述",
            key:'DiagnosisDesc',
            align: 'center'
          },
          {
            title:"疾病分级及分期",
            key:'DiseaseStagingAndGrading',
            align: 'center'
          },
          {
            title:"疾病治疗历史",
            key:'TreatmentDescription',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagShow(params.row.DiagnosisID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagEdit(params.row.DiagnosisID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagRemove(params.row.DiagnosisID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        diagnosisTableData:[],
        diagnosisTotalNum:0,
        diagMultipleSelection:[],
        diagPageNum:1,
        diagPageSize:10,
        molDiagnosisTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"分子诊断编号",
            key:'MolDiagnosisID',
            align: 'center'
          },
          {
            title:"分子诊断产品",
            key:'ProductName_cn',
            align: 'center'
          },
          {
            title:"送检机构",
            key:'InspectionInstitution',
            align: 'center'
          },
          {
            title:"送检医师",
            key:'Physician',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"报告人",
            key:'ReporterName',
            align: 'center'
          },
          {
            title:"审核人",
            key:'ReviewerName',
            align: 'center'
          },
          {
            title:"报告日期",
            key:'ReportDate',
            align: 'center'
          },
          {
            title:"分子诊断报告",
            key:'Report',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-download'
                  },
                  style: {
                    fontSize:'18px',
                    padding: '3px'
                  },
                  domProps: {
                    title: '下载'
                  },
                  on: {
                    click: () => {
                      // this.molDiagShow(params.row.MolDiagnosisID)
                    }
                  }
                }, '')
              ])
            }
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagShow(params.row.MolDiagnosisID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagEdit(params.row.MolDiagnosisID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagRemove(params.row.MolDiagnosisID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        molDiagnosisTableData:[],
        molDiagnosisTotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      subjectRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/subject/del.php",{SubjectID:[this.subjectID]},()=>{
          setTimeout(()=>{
            // history.back()
            this.$router.push("/subject")
          },1000)
        })
      },
      handleEdit(){
        this.$router.push("/editsubject?subjectid=" + this.subjectID)
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
      addDiagnosis(){
        //将当前受检者的ID传入
        this.$router.push("/adddiagnosis?subjectid=" + this.subjectID)
      },
      diagShow (id) {
        // console.log(id);
        this.$router.push("/browdiagnosis?subjectid=" + this.subjectID + "&diagnosisid=" + id)
      },
      diagEdit (id) {
        // console.log(id);
        this.$router.push("/editdiagnosis?subjectid=" + this.subjectID + "&diagnosisid=" + id)
      },
      diagRemove(id){
        // console.log(id);
        this.$Common.remove(this,'/php/subject/diagnosis/del.php',{DiagnosisID:[id]},()=>{
          this.diagHandlePage(this.diagPageNum);
          this.diagMultipleSelection = []
        })
      },
      diagBatchRemove(){
        let IDS = [];
        this.diagMultipleSelection.forEach(item=>{
          IDS.push(item.DiagnosisID);
        })
        this.$Common.batchRemove(this,IDS,'/php/subject/diagnosis/del.php',{DiagnosisID:IDS},()=>{
          this.diagHandlePage(this.diagPageNum);
          this.diagMultipleSelection = []
        })
      },
      diagHandleSelectionChange(val){
        this.diagMultipleSelection = val;
      },
      diagHandlePage(val){
        this.loading = true;
        this.diagPageNum = val;
        let obj = {
          SubjectID:this.subjectID,
          pageNum:this.diagPageNum,
          pageSize:this.diagPageSize
        }
        this.$Common.list(this,'/php/subject/diagnosis/index.php',obj,(response)=>{
          this.diagnosisTableData=response.data.data;
          this.diagnosisTotalNum=Number(response.data.num);
          this.loading = false;
        })
      },
      diagHandlePageSize(val){
        this.diagPageSize = val;
        this.diagPageNum = 1;
        this.diagHandlePage(this.diagPageNum)
      },
      addMolDiagnosis(){
        //将当前受检者的ID传入
        this.$router.push("/addmoldiagnosis?subjectid=" + this.subjectID)
      },
      molDiagShow (id) {
        // console.log(id);
        this.$router.push("/browmoldiagnosis?moldiagnosisid=" + id)
      },
      molDiagEdit (id) {
        // console.log(id);
        this.$router.push("/editmoldiagnosis?moldiagnosisid=" + id)
      },
      molDiagRemove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/moldiagnosis/del.php",{MolDiagnosisID:[id]},()=>{
          this.molDiagHandlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      molDiagBatchRemove(id){
        // console.log(id);
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.MolDiagnosisID)
        })
        this.$Common.batchRemove(this,IDS,"/php/moldiagnosis/del.php",{MolDiagnosisID:IDS},()=>{
          this.molDiagHandlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      molDiagHandleSelectionChange(val){
        this.multipleSelection = val;
      },
      molDiagHandlePage(val){
        this.molLoading = true;
        this.pageNum = val;
        let obj = {
          SubjectID:this.subjectID,
          pageNum:this.diagPageNum,
          pageSize:this.diagPageSize
        }
        this.$Common.list(this,'/php/moldiagnosis/edit.php',obj,(response)=>{
          this.molDiagnosisTableData=response.data.data;
          this.molDiagnosisTableNum=Number(response.data.num);
          this.molLoading = false;
        })
      },
      molDiagHandlePageSize(val){
        this.pageSize = val;
        this.PageNum = 1;
        this.molDiagHandlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .diagnosisCon{
      box-sizing:border-box;
      border-bottom:1px dashed #ccc;
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


