<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的文库</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的文库</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的文库</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="核酸编号" prop="NucleicAcidID">
            <Input v-model="addForm.NucleicAcidID" v-if="nucleicAcidID" disabled placeholder="请输入核酸编号" style="width: 280px"></Input>
            <AutoComplete v-model="addForm.NucleicAcidID" v-if="!nucleicAcidID" @on-search="searchNucleicAcidID" @on-select="selectNucleicAcidID" placeholder="请输入核酸编号" style="width:280px">
                <Option v-for="(item,index) in NucleicAcidData" :value="item.NucleicAcidID" :key="index">{{item.NucleicAcidID}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="文库公司编号" prop="LibSGIID">
            <Input v-model="addForm.LibSGIID" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建人"  prop="LibConsManipulatorID">
            <Select v-model="addForm.LibConsManipulatorID" placeholder="请输入文库构建人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="文库构建日期" prop="LibConsDate">
            <DatePicker type="date" @on-change="changeLibConsDate" show-week-numbers placement="bottom-start" placeholder="请选择文库构建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="addForm.LibConsAmount" placeholder="请输入文库构建用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量单位">
            <Input v-model="addForm.LibConsUnit" placeholder="请输入文库构建用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积">
              <Input v-model="addForm.LibVolume" placeholder="请输入文库体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积单位">
              <Input v-model="addForm.LibVolumeUnit" placeholder="请输入文库体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度">
              <Input v-model="addForm.LibConc" placeholder="请选择文库浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度单位">
              <Input v-model="addForm.LibConcUnit" placeholder="请选择文库浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量">
            <Input v-model="addForm.LibAmount" placeholder="请输入文库总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量单位">
            <Input v-model="addForm.LibAmountUnit" placeholder="请输入文库总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小">
            <Input v-model="addForm.LibInsSize" placeholder="请输入文库插入片段大小" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小单位">
            <Input v-model="addForm.LibInsSizeUnit" placeholder="请输入文库插入片段大小单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库质量">
            <Select v-model="addForm.LibQuality" placeholder="请选择文库质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="差">差</Option>
            </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="addForm.Status" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
      })
      if(this.nucleicAcidID===undefined){
        let obj = {}
        obj.NucleicAcidID = this.addForm.NucleicAcidID
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.NucleicAcidArr = response.data.data
          this.NucleicAcidData = response.data.data
        })
      }else{
        this.addForm.NucleicAcidID=this.nucleicAcidID
      }
    },
    data() {
      return {
        nucleicAcidID:location.search.split("=")[1],
        employeeData:[],
        NucleicAcidArr:[],
        NucleicAcidData:[],
        addForm: {
          NucleicAcidID:'',
          LibSGIID:'',
          LibConsManipulatorID:'',
          LibConsDate:'',
          LibConsAmount:'',
          LibConsUnit:'',
          LibVolume:'',
          LibVolumeUnit:'',
          LibConc:'',
          LibConcUnit:'',
          LibAmount:'',
          LibAmountUnit:'',
          LibInsSize:'',
          LibInsSizeUnit:'',
          LibQuality:'',
          Status:''//数字
        },
        rule:{
          NucleicAcidID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibConsManipulatorID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibConsDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      changeLibConsDate(date){
        this.addForm.LibConsDate = date
      },
      searchNucleicAcidID(value){
        let obj = {}
        obj.NucleicAcidID = value
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.NucleicAcidArr = response.data.data
          this.NucleicAcidData = response.data.data
        })
      },
      selectNucleicAcidID(value){
        this.NucleicAcidData = this.NucleicAcidArr.filter(item=>{
          return item.NucleicAcidID === value
        })
        this.addForm.NucleicAcidID = value
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            console.log(obj);
            this.$Common.add(this,"/php/library/add.php",obj,()=>{
              // 
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


