<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">信息分析流程管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="150">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程编号">
            <Input v-model="editForm.PipelineID" disabled placeholder="请输入信息分析流程编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程项目编号" prop="PipelineProjectID">
            <Input v-model="editForm.PipelineProjectID" placeholder="请输入信息分析流程项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <AutoComplete v-model="editForm.ProductName_cn" @on-search="searchName" @on-select="selectName" placeholder="请先输入产品中文名称" style="width:280px">
              <Option v-for="(item,index) in productData" :value="item.ProductName_cn" :key="index">{{item.ProductName_cn}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="产品版本号" prop="Version">
            <AutoComplete v-model="editForm.Version" :disabled="versionStep" @on-search="searchVersion" @on-select="selectVersion" placeholder="再输入产品版本号" style="width:280px">
              <Option v-for="(item,index) in versionData" :value="item.Version" :key="index">{{item.Version}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        // 获取pipeline详情
        this.$http.post('/php/pipeline/list.php',qs.stringify({PipelineID:this.pipelineID})).then(response=>{
          // console.log(response.data);
          this.editForm = response.data.data[0]
          resolve()
        })
      }).then(()=>{
        // 获取产品中文名称
        this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:this.editForm.ProductName_cn})).then(response=>{
          // console.log(response.data);
          this.productArr = response.data.data
          this.productData = response.data.data
        })
        // this.$http.post("/php/datainterface/getProductNameByNum.php",qs.stringify({ProductName_cn:this.editForm.ProductName_cn})).then(response=>{
        //   // console.log(response.data);
        //   this.versionArr = response.data
        //   this.versionData = response.data
        // })
      })
      
      
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        pipelineID:location.search.split('=')[1],
        productArr:[],
        productData:[],
        versionArr:[],
        versionData:[],
        versionStep:true,
        editForm:{},
        rule:{
          PipelineProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchName(value){
        this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          this.productArr = response.data.data
          this.productData = response.data.data
        })
      },
      selectName(value){
        this.$http.post("/php/datainterface/getProductNameByNum.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          if(response.data.length==0){
            this.$Message.warning("该产品尚没有版本号！")
            return false;
          }
          this.versionArr = response.data
          this.versionData = response.data
          this.versionStep = false;
          this.editForm.Version = ""
        })
      },
      searchVersion(value){
        this.versionData = this.versionArr.filter(item=>{
          return item.Version.indexOf(value)!==-1
        })
      },
      selectVersion(value){
        this.editForm.ProductUpdateRecordID = this.versionData.filter(item=>{
          return item.ProductName_cn===this.editForm.ProductName_cn&&item.Version===value
        })[0].ProductUpdateRecordID
        // console.log(this.editForm);
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            obj.PipelineID = this.editForm.PipelineID
            obj.PipelineProjectID = this.editForm.PipelineProjectID
            obj.ProductUpdateRecordID = this.editForm.ProductUpdateRecordID
            this.$Common.edit(this,"/php/pipeline/update.php",obj,()=>{
              this.$router.push("/pipeline")
            })
          }
        })
      },
      backList(){
        this.$router.push("/pipeline")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


