<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + libID">查看文库详情</BreadcrumbItem>
      <BreadcrumbItem>添加新的QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的QC</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的QC</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="addForm.LibID" disabled placeholder="请输入文库编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总碱基数" prop="TotalBases">
            <Input v-model="addForm.TotalBases" placeholder="请输入总碱基数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总序列数" prop="TotalReadNumber">
            <Input v-model="addForm.TotalReadNumber" placeholder="请输入总序列数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="重复率" prop="DuplicationRate">
            <Input v-model="addForm.DuplicationRate" placeholder="请输入重复率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="比对比率" prop="MappingRate">
              <Input v-model="addForm.MappingRate" placeholder="请输入比对比率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="序列长度" prop="ReadLength">
              <Input v-model="addForm.ReadLength" placeholder="请输入序列长度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="捕获效率" prop="CaptureEfficacy">
              <Input v-model="addForm.CaptureEfficacy" placeholder="请输入捕获效率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针平均深度" prop="ProbeMeanDepth">
              <Input v-model="addForm.ProbeMeanDepth" placeholder="请输入探针平均深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针中点深度" prop="ProbeMedianDepth">
            <Input v-model="addForm.ProbeMedianDepth" placeholder="请输入探针中点深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针均一性" prop="ProbeUniformity">
            <Input v-model="addForm.ProbeUniformity" placeholder="请输入探针均一性" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率" prop="DepthGT1Rate">
            <Input v-model="addForm.DepthGT1Rate" placeholder="请输入覆盖率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率≥30" prop="DepthGT30Rate">
            <Input v-model="addForm.DepthGT30Rate" placeholder="请输入覆盖率≥30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="addForm.Status" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
        </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        libID:location.search.split('=')[1],
        addForm: {
          LibID:location.search.split('=')[1],
          TotalBases:'111111',
          TotalReadNumber:'1000',
          DuplicationRate:'10',
          MappingRate:'10',
          ReadLength:'10',
          CaptureEfficacy:'10',
          ProbeMeanDepth:'10',
          ProbeMedianDepth:'10',
          ProbeUniformity:'10',
          DepthGT1Rate:'10',
          DepthGT30Rate:'5',
          Status:'1'
        },
        rule:{
          LibID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          TotalBases:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          TotalReadNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DuplicationRate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          MappingRate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ReadLength:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          CaptureEfficacy:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeMeanDepth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeMedianDepth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeUniformity:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DepthGT1Rate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DepthGT30Rate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/qc/add.php",obj,()=>{
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


