<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browsubject?subjectid=' + subjectID">查看受检者详情</BreadcrumbItem>
      <BreadcrumbItem>添加新的临床诊疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的临床诊疗</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的临床诊疗</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="受检者编号" prop="SubjectID">
            <Input v-model="addForm.SubjectID" placeholder="请输入受检者编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗描述" prop="DiagnosisDesc">
            <Input v-model="addForm.DiagnosisDesc" type="textarea" placeholder="请输入临床诊疗描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分级及分期" prop="DiseaseStagingAndGrading">
            <Input v-model="addForm.DiseaseStagingAndGrading" placeholder="请输入疾病分级及分期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病治疗历史" prop="TreatmentDescription">
            <Input v-model="addForm.TreatmentDescription" type="textarea" placeholder="请输入疾病治疗历史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.addForm.SubjectID = this.subjectID
    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
        addForm: {
          SubjectID:'',
          DiagnosisDesc:'',
          DiseaseStagingAndGrading:'',
          TreatmentDescription:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          DiagnosisDesc:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          DiseaseStagingAndGrading:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          TreatmentDescription:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/subject/diagnosis/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


