<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + editForm.LibID">查看文库</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browvariant'">查看QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editvariant'">编辑QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="variantRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="editForm.LibID" disabled placeholder="请输入文库编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异编号" prop="VariantID">
            <Input v-model="editForm.VariantID" disabled placeholder="请输入变异编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Select v-model="editForm.ChrName" :disabled="currentPath=='browvariant'" placeholder="请选择染色体名称" style="width: 280px">
              <Option v-for="(item,index) in chrArr":key="index" :value="item.ChrName">{{item.ChrName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="染色体位置" prop="Position">
            <Input v-model="editForm.Position" :disabled="currentPath=='browvariant'" placeholder="请输入染色体位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="editForm.Symbol" :disabled="currentPath=='browvariant'" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本">
            <Input v-model="editForm.transcript" :disabled="currentPath=='browvariant'" placeholder="请输入转录本" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF序列">
            <Input v-model="editForm.Ref" :disabled="currentPath=='browvariant'" placeholder="请输入REF序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="editForm.Alt" :disabled="currentPath=='browvariant'" placeholder="请输入ALT序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Select v-model="editForm.VariantType" :disabled="currentPath=='browvariant'" placeholder="请选择变异类型" style="width: 280px">
              <Option v-for="(item,index) in variantTypeArr":key="index" :value="item.VariantTypeID">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="基因型">
              <Input v-model="editForm.Genotype" :disabled="currentPath=='browvariant'" placeholder="请输入基因型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因频率">
              <Input v-model="editForm.MinorAlleleFraction" :disabled="currentPath=='browvariant'" placeholder="请输入基因频率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="测序深度">
              <Input v-model="editForm.DepthOfCoverage" :disabled="currentPath=='browvariant'" placeholder="请输入测序深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="突变结果">
              <Input v-model="editForm.Consequence" :disabled="currentPath=='browvariant'" placeholder="请输入突变结果" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.exon_intron" :disabled="currentPath=='browvariant'" placeholder="请输入exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron位置">
              <Input v-model="editForm.Location" :disabled="currentPath=='browvariant'" placeholder="请输入exon/intron位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="editForm.HGVSp" :disabled="currentPath=='browvariant'" placeholder="请输入HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="editForm.HGVSc" :disabled="currentPath=='browvariant'" placeholder="请输入HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="editForm.rsID" :disabled="currentPath=='browvariant'" placeholder="请输入rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="editForm.Clin_Sig" :disabled="currentPath=='browvariant'" placeholder="请输入临床意义" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="标记">
            <Input v-model="editForm.Flag" :disabled="currentPath=='browvariant'" placeholder="请输入标记" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="影响">
            <Input v-model="editForm.Impact" :disabled="currentPath=='browvariant'" placeholder="请输入影响" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browvariant'">编辑</Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editvariant'">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/datainterface/getVariantByType.php",{},(response)=>{
        this.variantTypeArr = response.data
      })
      this.$Common.list(this,"/php/datainterface/getChr.php",{},(response)=>{
        this.chrArr = response.data
      })
      this.$Common.list(this,"/php/snp_indel/edit.php",{VariantID:this.variantID},(response)=>{
        this.editForm = response.data.data[0];
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        variantID:location.search.split('=')[1],
        variantTypeArr:[],
        chrArr:[],        
        editForm:{},
        rule:{
          LibID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Position:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Symbol:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      variantRemove(){
        this.$Common.remove(this,"/php/snp_indel/del.php",{VariantID:[this.variantID]},()=>{
          history.back()
        })
      },
      handleEdit(){
        this.currentPath="editvariant"
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/snp_indel/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


