<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">分子诊断管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="受检人编号" prop="SubjectID">
          <Input v-model="editForm.SubjectID" :disabled="editForm.SubjectID!==''" placeholder="请输入受检人编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检人姓名" prop="SubjectName">
          <Input v-model="editForm.SubjectName" disabled placeholder="请输入受检人姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子诊断编号" prop="MolDiagnosisID">
          <Input v-model="editForm.MolDiagnosisID" disabled placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检日期" prop="InspectionDate">
            <DatePicker type="date" @on-change="changeInspectionDate" :value="editForm.InspectionDate" show-week-numbers placeholder="请输入送检日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="送检医师" prop="Physician">
            <Input v-model="editForm.Physician" placeholder="请输入送检医师" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检机构" prop="InspectionInstitution">
            <Input v-model="editForm.InspectionInstitution" placeholder="请输入送检机构" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2>产品名称：</h2>
            <FormItem label="Panel名称">
              <Input v-model="editForm.PanelName_cn" disabled placeholder="请输入Panel名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product名称">
              <AutoComplete v-model="editForm.ProductName_cn" @on-search="searchProductName" @on-select="selectProductName" placeholder="请先输入产品中文名称" style="width:280px">
                <Option v-for="(item,index) in productData" :value="item.ProductName_cn" :key="index">{{item.ProductName_cn}}</Option>
              </AutoComplete>
            </FormItem>
            <FormItem label="Pipeline名称">
              <Input v-model="editForm.PipelineName_cn" disabled placeholder="请输入Pipeline名称" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="状态">
            <Input v-model="editForm.Status" placeholder="请输入状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告人">
            <Select v-model="editForm.ReporterID" placeholder="请输入报告人" style="width:280px" placement="top">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="审阅人">
            <Select v-model="editForm.ReviewerID" placeholder="请输入审阅人" style="width:280px" placement="top">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="报告日期">
            <DatePicker type="date" @on-change="changeReportDate" :value="editForm.InspectionDate" show-week-numbers placeholder="请输入报告日期" style="width: 280px" placement="top"></DatePicker>
          </FormItem> 
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/moldiagnosis/edit.php",{MolDiagnosisID:this.molDiagnosisID},(response)=>{
        this.editForm = response.data.data[0]
          this.$http.post("/php/datainterface/getSubjectByName.php").then(response=>{
          // console.log(response.data);
          this.editForm.SubjectName = response.data.filter(item=>{
            return item.SubjectID===this.editForm.SubjectID
          })[0].SubjectName
        })
        //获取报告人和审阅人的编号和姓名
        this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
          // console.log(response.data);
          this.employeeData = response.data
          // this.reviewerData = response.data
          this.editForm.ReporterID = this.employeeData.filter(item=>{
            return item.Fullname === this.editForm.ReporterName
          })[0].EmployeeID
          this.editForm.ReviewerID = this.employeeData.filter(item=>{
            return item.Fullname === this.editForm.ReviewerName
          })[0].EmployeeID
        })
      })
      //获取product的ID和名称
      this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:this.editForm.ProductName_cn})).then(response=>{
        // console.log(response.data);
        this.productArr = response.data.data
        this.productData = response.data.data
      })
      
    },
    data() {
      return {
        molDiagnosisID:location.search.split("=")[1],
        subjectIDArr:[],
        subjectIDData:[],
        subjectNameArr:[],
        subjectNameData:[],
        productArr:[],
        productData:[],
        employeeData:[],
        editForm:{},
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SubjectName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          MolDiagnosisID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          InspectionDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Physician:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          InspectionInstitution:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      changeInspectionDate(date){
        this.editForm.InspectionDate = date
      },
      changeReportDate(date){
        this.editForm.ReportDate = date
      },
      searchProductName(value){
        this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          this.productArr = response.data.data
          this.productData = response.data.data
        })
      },
      selectProductName(value){
        this.editForm.ProductName_cn = value
        this.productData = this.productArr.filter(item=>{
          return item.ProductName_cn===value;
        })
        this.editForm.ProductID = this.productData[0].ProductID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            // this.$router.push('/moldiagnosis')
            let obj={}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/moldiagnosis/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


