<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browsubject?subjectid=' + subjectID">查看受检者详情</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browdiagnosis'">查看临床诊疗</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='editdiagnosis'">编辑临床诊疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
        <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检者编号" prop="SubjectID">
            <Input v-model="editForm.SubjectID" disabled placeholder="请输入受检者编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗编号" prop="DiagnosisID">
            <Input v-model="editForm.DiagnosisID" disabled placeholder="请输入临床诊疗编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗描述" prop="DiagnosisDesc">
            <Input v-model="editForm.DiagnosisDesc" :disabled="currentPath==='browdiagnosis'" type="textarea" placeholder="请输入临床诊疗描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分级及分期" prop="DiseaseStagingAndGrading">
            <Input v-model="editForm.DiseaseStagingAndGrading" :disabled="currentPath==='browdiagnosis'" placeholder="请输入疾病分级及分期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病治疗历史" prop="TreatmentDescription">
            <Input v-model="editForm.TreatmentDescription" :disabled="currentPath==='browdiagnosis'" type="textarea" placeholder="请输入疾病治疗历史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit" v-if="currentPath==='browdiagnosis'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editdiagnosis'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>

var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/subject/diagnosis/index.php",{DiagnosisID:this.diagnosisID},(response)=>{
        this.editForm = response.data.data[0]
      })
      // this.$http.post('/php/subject/diagnosis/index.php',qs.stringify({DiagnosisID:this.diagnosisID})).then(response=>{
      //   // console.log(response.data);
      //   this.editForm = response.data.data[0]
      // })
    },
    data() {
      return {
        currentPath:location.pathname.split("/")[1],
        subjectID:location.search.split('&')[0].split('=')[1],
        diagnosisID:location.search.split('&')[1].split('=')[1],
        editForm:{},
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          DiagnosisDesc:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          DiseaseStagingAndGrading:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ],
          TreatmentDescription:[
            {
              required: true, message: '不能为空', trigger: 'blur,chage'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editdiagnosis";
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/subject/diagnosis/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
            // this.$http.post("/php/subject/diagnosis/edit.php",qs.stringify(obj)).then(response=>{
            //   if(response.data.code===0){
            //     this.$Message.success('保存成功！即将跳转页面...')
            //     setTimeout(()=>{
            //       this.$router.push("/browsubject?subjectid=" + this.subjectID)
            //     },1000)
            //   }else{
            //     this.$Message.error('保存失败！' + response.data.info)
            //   }
            // })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


