<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="nucleicAcidRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
         <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="editForm.LatestupdateDate" disabled  placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸编号" prop="NucleicAcidSGIID">
            <Input v-model="editForm.NucleicAcidID" placeholder="请输入核酸编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本编号" prop="BiospeciemensID">
            <Input v-model="editForm.BiospeciemensID" placeholder="请输入样本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="NucleicAcidSGIID">
            <Input v-model="editForm.NucleicAcidSGIID" placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="NAExtractionDate">
            <DatePicker @on-change="changeNAExtractionDate" show-week-numbers placement="bottom-start" placeholder="请选择核酸提取日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="核酸提取人" prop="NAExtractionManipulatorID">
            <Select v-model="editForm.NAExtractionManipulatorID" placeholder="请输入核酸提取人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="editForm.NAExtractionAmount" placeholder="请输入核酸提取用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="editForm.NAExtractionUnit" placeholder="请输入核酸提取用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="editForm.NAVolume" placeholder="请输入核酸体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="editForm.NAVolumeUnit" placeholder="请输入核酸体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="editForm.NAConc" placeholder="请输入核酸浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="editForm.NAConcUnit" placeholder="请输入核酸浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="editForm.NAAmount" placeholder="请输入核酸总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="editForm.NAAmountUnit" placeholder="请输入核酸总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Select v-model="editForm.NAQuality" placeholder="请选择核酸质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="坏">坏</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="editForm.NAType" placeholder="请输入核酸类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.Status" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
        // this.reviewerData = response.data
      })
      this.$Common.list(this,"/php/nucleicacids/index.php",{NucleicAcidID:this.nucleicAcidID},(response)=>{
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        nucleicAcidID:location.search.split('=')[1],
        employeeData:[],
        editForm:{},
        rule:{
          BiospeciemensID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          NucleicAcidSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          NAExtractionManipulatorID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          NAExtractionDate:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      nucleicAcidRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/nucleicacids/del.php",{NucleicAcidID:[this.nucleicAcidID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })  
      },
      changeNAExtractionDate(date){
        this.addForm.NAExtractionDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/nucleicacids/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


