<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="subjectRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="姓" prop="LastName">
            <Input v-model="editForm.LastName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="中间名">
            <Input v-model="editForm.MiddleName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="名" prop="FirstName">
            <Input v-model="editForm.FirstName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="出生日期" prop="DateOfBirth">
            <DatePicker @on-change="handleChange" :value="editForm.DateOfBirth" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择出生日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="性别" prop="Gender">
            <RadioGroup v-model="editForm.Gender" placeholder="请选择性别" style="width: 280px">
              <Radio label="m">男</Radio>
              <Radio label="f">女</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="证件类型" prop="IDType">
            <RadioGroup v-model="editForm.IDType" placeholder="请选择证件类型" style="width: 280px">
              <Radio label="二代身份证">二代身份证</Radio>
              <Radio label="护照">护照</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="证件号码" prop="IDNumber">
            <Input v-model="editForm.IDNumber" placeholder="请输入证件号码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="手机号码" prop="PhoneNumber">
            <Input v-model="editForm.PhoneNumber" placeholder="请输入手机号码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="邮箱地址">
            <Input v-model="editForm.Efront" placeholder="请输入邮箱名" style="width: 280px">
              <Select v-model="editForm.Eback" slot="append"  placeholder="请选择邮箱后缀" style="width: 120px">
                <Option value="@163.com">@163.com</Option>
                <Option value="@qq.com">@qq.com</Option>
                <Option value="@sina.com">@sina.com</Option>
              </Select>
            </Input>
          </FormItem>
          <FormItem label="家族史">
            <Input v-model="editForm.familyHistory" type="textarea" placeholder="请输入家族史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/subject/index.php",{SubjectID:this.subjectID},(response)=>{
        this.editForm = response.data.data[0]
        if(this.editForm.Email.trim()){
          this.editForm.Efront=this.editForm.Email.split("@")[0]
          this.editForm.Eback="@" + this.editForm.Email.split("@")[1]
        }else{
          this.editForm.Efront=''
          this.editForm.Eback="@163.com"
        }
      })
    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{},
        rule:{
          LastName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          FirstName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DateOfBirth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Gender:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          IDType:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          IDNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PhoneNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      subjectRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/subject/del.php",{SubjectID:[this.subjectID]},()=>{
          setTimeout(()=>{
            // history.back()
            this.$router.push("/subject")
          },1000)
        })
      },
      handleChange(date){
        // console.log(date);
        this.editForm.DateOfBirth = date;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //判断Email是否应为空
            if(!this.editForm.Efront.trim()&&!this.editForm.Eback.trim()){
              this.editForm.Email = this.editForm.Efront.trim()+this.editForm.Eback.trim();
            }
            this.editForm.Age = String(this.editForm.Age)
            let obj = {};
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            this.$Common.edit(this,"/php/subject/edit.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


