<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="nucleicAcidRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="120">
         <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸编号" prop="NucleicAcidSGIID">
            <Input v-model="browForm.NucleicAcidID" readonly placeholder="请输入核酸编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本编号" prop="BiospeciemensID">
            <Input v-model="browForm.BiospeciemensID" readonly placeholder="请输入样本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="NucleicAcidSGIID">
            <Input v-model="browForm.NucleicAcidSGIID" readonly placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="NAExtractionDate">
            <DatePicker v-model="browForm.NAExtractionDate" show-week-numbers placement="bottom-start" readonly placeholder="请选择核酸提取日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="核酸提取人" prop="Name">
            <Input v-model="browForm.Name" readonly placeholder="请输入核酸提取人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="browForm.NAExtractionAmount" readonly placeholder="请输入核酸提取用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="browForm.NAExtractionUnit" readonly placeholder="请输入核酸提取用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="browForm.NAVolume" readonly placeholder="请输入核酸体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="browForm.NAVolumeUnit" readonly placeholder="请输入核酸体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="browForm.NAConc" readonly placeholder="请输入核酸浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="browForm.NAConcUnit" readonly placeholder="请输入核酸浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="browForm.NAAmount" readonly placeholder="请输入核酸总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="browForm.NAAmountUnit" readonly placeholder="请输入核酸总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Input v-model="browForm.NAQuality" readonly placeholder="请选择核酸质量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="browForm.NAType" readonly placeholder="请输入核酸类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="browForm.Status" readonly placeholder="请选择状态" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addLibrary">添加新文库</Button>
          </div>
          <div class="chromoListcontainer">
            <Table stripe border height="200" :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/nucleicacids/index.php",{NucleicAcidID:this.nucleicAcidID},(response)=>{
        this.browForm = response.data.data[0]
      })
      //获取文库列表
      this.handlePage(this.pageNum)
    },
    data() {
      return {
        nucleicAcidID:location.search.split("=")[1],
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"文库编号",
            key:'LibID',
            align: 'center'
          },
          {
            title:"文库公司编号",
            key:'LibSGIID',
            align: 'center'
          },
          {
            title:"文库构建用量及单位",
            key:'LibConsAmount',
            align: 'center'
          },
          {
            title:"文库构建日期",
            key:'LibConsDate',
            align: 'center'
          },
          {
            title:"文库构建人",
            key:'LibConsManipulator',
            align: 'center'
          },
          {
            title:"文库质量",
            key:'LibQuality',
            align: 'center'
          },
          {
            title:"文库体积及单位",
            key:'LibVolume',
            align: 'center'
          },
          {
            title:"文库浓度及单位",
            key:'LibConc',
            align: 'center'
          },
          {
            title:"文库总量及单位",
            key:'LibAmount',
            align: 'center'
          },
          {
            title:"文库插入片段大小",
            key:'LibInsSize',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.LibID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.LibID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.LibID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      nucleicAcidRemove(){
        // console.log(id);
        this.$Common.remove(this,"/php/nucleicacids/del.php",{NucleicAcidID:[this.nucleicAcidID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })  
      },
      handleEdit(){
        this.$router.push("/editnucleicacid?nucleicacidid=" + this.nucleicAcidID)
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
      addLibrary(){
        this.$router.push("/addlibrary?nucleicacidid=" + this.nucleicAcidID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browlibrary?libid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editlibrary?libid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/library/del.php",{LibID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })   
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.LibID)
        })
        this.$Common.batchRemove(this,IDS,"/php/library/del.php",{LibID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {
          NucleicAcidID:this.nucleicAcidID,
          pageNum:this.pageNum,
          pageSize:this.pageSize
        }
        this.$Common.list(this,"/php/library/edit.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .chromoListcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:5px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding: 2px;
      }
    }
  }
}
</style>


