<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>样本管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="样本公司编号">
            <Input v-model="searchForm.SampleSGIID" placeholder="请输入样本编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="病理编号">
            <Input v-model="searchForm.PathologicalNumber" placeholder="请输入病理编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="样本类型">
            <Select v-model="searchForm.SampleType" placeholder="请选择样本类型" style="width: 162px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="取样部位">
              <Input v-model="searchForm.Site" placeholder="请输入取样部位" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="取样日期">
            <DatePicker type="daterange" v-model="searchForm.SamplingDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择取样日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期">
            <DatePicker type="daterange" v-model="searchForm.ReceivedDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择收样日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
              <Input v-model="searchForm.Name" placeholder="请输入收样人" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
            <Select v-model="searchForm.IsTumorCell" placeholder="请选择是否为肿瘤细胞" style="width: 162px">
              <Option value="是">是</Option>
              <Option value="否">否</Option>
            </Select>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="searchForm.TumorCellContent" placeholder="请输入肿瘤细胞含量" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="searchForm.Quantity" placeholder="请输入样本量" style="width: 162px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addBiospeciemens">添加新样本</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)

      this.handlePage(this.pageNum)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          SampleSGIID:'',
          PathologicalNumber:'',
          SampleType:'',
          Site:'',
          SamplingDate:'',
          ReceivedDate:'',
          Name:'',
          IsTumorCell:'',
          TumorCellContent:'',
          Quantity:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"分子诊断编号",
            key:'MolDiagnosisID',
            align: 'center'
          },
          {
            title:"样本编号",
            key:'BiospeciemensID',
            align: 'center'
          },
          {
            title:"病理编号",
            key:'PathologicNumber',
            align: 'center'
          },
          {
            title:"样本原始编号",
            key:'SampleOriID',
            align: 'center'
          },
          {
            title:"样本公司编号",
            key:'SampleSGIID',
            align: 'center'
          },
          {
            title:"样本类型",
            key:'SampleType',
            align: 'center'
          },
          {
            title:"取样部位",
            key:'Site',
            align: 'center'
          },
          {
            title:"取样日期",
            key:'SamplingDate',
            align: 'center'
          },
          {
            title:"收样日期",
            key:'ReceivedDate',
            align: 'center'
          },
          {
            title:"收样人",
            key:'Name',
            align: 'center'
          },
          {
            title:"是否为肿瘤细胞",
            key:'IsTumorCell',
            align: 'center'
          },
          {
            title:"肿瘤细胞含量",
            key:'TumorCellContent',
            align: 'center'
          },
          {
            title:"样本量",
            key:'Quantity',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.BiospeciemensID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.BiospeciemensID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.BiospeciemensID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addBiospeciemens(){
        this.$router.push("/addbiospeciemens")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browbiospeciemens?biospeciemensid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editbiospeciemens?biospeciemensid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/biospecimens/del.php",{BiospeciemensID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection=[]
        })   
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.BiospeciemensID)
        })
        this.$Common.batchRemove(this,IDS,"/php/biospecimens/del.php",{BiospeciemensID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection=[]
        })   
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        console.log(this.searchForm);
        let obj = {}
        for(var key in this.searchForm){
          if(key!=='SamplingDate'&&key!=='ReceivedDate'){
            obj[key]=this.searchForm[key].trim()
          }else{
            //判断日期是不是由有值变为空
            this.searchForm[key][0]===''?obj[key]=[]:obj[key]=this.searchForm[key]
          }
        }
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$Common.list(this,"/php/biospecimens/index.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


