<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的产品</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的产品</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的产品</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <Input v-model="addForm.ProductName_cn" placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品英文名称" prop="ProductName_en">
            <Input v-model="addForm.ProductName_en" placeholder="请输入产品英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称" prop="PanelName_cn">
            <AutoComplete v-model="addForm.PanelName_cn" @on-search="searchName" @on-select="selectName" placeholder="请输入Panel中文名称" style="width:280px">
              <Option v-for="(item,index) in panelNameData" :value="item.PanelName_cn" :key="index">{{item.PanelName_cn}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="Panel版本号" prop="Version">
            <AutoComplete v-model="addForm.Version" :disabled='versionStep' @on-search="searchVersion" @on-select="selectVersion" placeholder="请输入Panel版本号" style="width:280px">
              <Option v-for="(item,index) in panelVersionData" :value="item.Version" :key="index">{{item.Version}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getPanelByName.php").then(response=>{
        // console.log(response.data);
        this.panelNameArr = response.data
        this.panelNameData = response.data
      })
    },
    data() {
      return {
        panelNameArr:[],
        panelNameData:[],
        panelVersionArr:[],
        panelVersionData:[],
        versionStep:true,
        addForm: {
          PanelUpdateRecordID:'',
          PanelID:'',
          PanelName_cn:'',
          Version:'',
          ProductName_cn:'',
          ProductName_en:'',
        },
        rule:{
          PanelName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchName(value){
        this.$http.post("/php/datainterface/getPanelByName.php",qs.stringify({PanelName_cn:value})).then(response=>{
          // console.log(response.data);
          this.panelNameArr = response.data
          this.panelNameData = response.data
          if(this.panelNameData.length===0){
            this.panelVersionArr = []
            this.panelVersionData = []
            this.$Message.warning('panel中文名称不存在，请从下拉框中选择！')
          }
        })
      },
      selectName(value){
        this.addForm.PanelName_cn = value;
        this.panelNameData = this.panelNameArr.filter(item=>{
          return item.PanelName_cn===value
        })
        this.addForm.PanelID = this.panelNameData[0].PanelID
        this.$http.post("/php/datainterface/getPanelVsByID.php",qs.stringify({PanelID:this.addForm.PanelID})).then(response=>{
          // console.log(response.data);
          this.panelVersionArr = response.data
          this.panelVersionData = response.data
          this.versionStep = false;
        })
      },
      searchVersion(value){
        this.panelVersionData = this.panelVersionArr.filter(item=>{
          return item.Version.toUpperCase().indexOf(value.toUpperCase()) !== -1;
        })
        if(this.panelVersionData.length===0){
          this.addForm.PanelUpdateRecordID='';
          this.$Message.warning('panel版本号不存在，请从下拉框中选择！')
        }
      },
      selectVersion(value){
        this.panelVersionData = this.panelVersionArr.filter(item=>{
          return item.Version === value;
        })
        this.addForm.PanelUpdateRecordID = this.panelVersionData[0].PanelUpdateRecordID
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.panelVersionData.length===0){
              this.addForm.PanelUpdateRecordID='';
              this.$Message.error('添加失败！panel版本号不存在')
              return false;
            }
            let obj = {
              PanelUpdateRecordID:this.addForm.PanelUpdateRecordID,
              ProductName_cn:this.addForm.ProductName_cn,
              ProductName_en:this.addForm.ProductName_en
            }
            this.$Common.add(this,"/php/product/add.php",obj,()=>{
              this.$router.push("/product")
            })
          }
        })
      },
      backList(){
        this.$router.push("/product")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


