<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>信息分析流程管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="140">
          <FormItem label="信息分析流程项目编号">
            <Input v-model="searchForm.PipelineProjectID" placeholder="请输入信息分析流程项目编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="信息分析流程中文名称">
            <Input v-model="searchForm.PipelineName_cn" placeholder="请输入信息分析流程中文名称" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="信息分析流程英文名称">
            <Input v-model="searchForm.PipelineName_en" placeholder="请输入信息分析流程英文名称" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="daterange" @on-change="formatCreateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker type="daterange" @on-change="formatLatestDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addPanel">添加新信息分析流程</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.handlePage(this.pageNum)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          PipelineID:'',
          PipelineProjectID:'',
          PipelineName_cn:'',
          PipelineName_en:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"信息分析流程项目编号",
            key:'PipelineProjectID',
            align: 'center'
          },
          {
            title:"信息分析流程中文名称",
            key:'PipelineName_cn',
            align: 'center'
          },
          {
            title:"信息分析流程英文名称",
            key:'PipelineName_en',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.PipelineID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.PipelineID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.PipelineID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      formatCreateDate(date){
        this.searchForm.CreateDate=date
      },
      formatLatestDate(date){
        this.searchForm.LatestUpdateDate=date
      },
      handleSubmit(name) {
        this.pageSize = 10;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      },
      addPanel(){
        this.$router.push("/addpipeline")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browpipeline?pipelineid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editpipeline?pipelineid=" + id)
      },
      remove(id){
        // console.log(id);       
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        for(var key in this.searchForm){
          if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
            obj[key]=this.searchForm[key].trim()
          }else{
            //判断日期是不是由有值变为空
            this.searchForm[key][0]===''?obj[key]=[]:obj[key]=this.searchForm[key]
          }
        }
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        // console.log(obj);
        this.$http.post("/php/pipeline/list.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


