<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/user">用户管理</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browuser'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='edituser'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="editForm.LatestupdateDate" disabled placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="邮箱" prop="email">
            <Input v-model="editForm.email" :disabled="currentPath==='browuser'" placeholder="请输入文库公司编号" style="width: 280px"></Input>
            <Button type="text" size="large" class="resetPwdBtn" @click="changePwd">修改密码（用户修改自己密码时显示）</Button>
            <Button type="text" size="large" class="resetPwdBtn" @click="resetPwd">重置密码（root修改其他用户密码时显示，重置为123456）</Button>
          </FormItem>
          <FormItem label="LastName" prop="LastName">
            <Input v-model="editForm.LastName" :disabled="currentPath==='browuser'" placeholder="请输入LastName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="MiddleName">
            <Input v-model="editForm.MiddleName" :disabled="currentPath==='browuser'" placeholder="请输入MiddleName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="FirstName" prop="FirstName">
            <Input v-model="editForm.FirstName" :disabled="currentPath==='browuser'" placeholder="请输入FirstName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="权限" prop="authority">
            <Select v-model="editForm.authority" :disabled="currentPath==='browuser'" placeholder="请选择权限" style="width: 280px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="状态" prop="status">
            <Select v-model="editForm.status" :disabled="currentPath==='browuser'" placeholder="请选择状态" style="width: 280px">
              <Option value="1111">启用</Option>
              <Option value="2222">禁用</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" v-if="currentPath==='browuser'" @click="handleEdit">编辑</Button>
              <Button type="primary" v-if="currentPath==='edituser'" @click="handleSubmit('editForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        currentPath:location.pathname.split("/")[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestupdateDate:'111',
          email:'111',
          LastName:'111',
          MiddleName:'111',
          FirstName:'111',
          authority:'1111',
          status:'1111'
        },
        rule:{
          email:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          LastName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          FirstName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          authority:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          status:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="edituser"
      },
      changePwd(){
        this.$router.push("/changepwd")
      },
      resetPwd(){
        //发送请求将用户密码重置为初始密码123456

      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/user")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    .resetPwdBtn{
      color:@themeColor;
      &:hover{
        text-decoration:underline;
      }
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


