import Vue from 'vue'
import axios from 'axios'
Vue.prototype.$http = axios
var qs = require('qs')
const Common = {}

Common.send = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    callback(response)
  })
}

/* 
  列表：根据返回值渲染表格
    that: 当前函数调用时的上下文
    url: 后台地址
    obj: 需传给后台的参数
    callback: 成功回调
*/
Common.list = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    callback(response)
  })
}

/* 
  添加：将当前详情保存至后台
    that: 当前函数调用时的上下文
    url: 后台地址
    obj: 需传给后台的参数
    callback: 成功回调
*/
Common.add = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    if(response.data.code===0){
      that.$Message.success('添加成功！即将跳转页面...')
      setTimeout(()=>{
        callback()
      },1000)
    }else{
      that.$Message.error('添加失败！' + response.data.info)
    }
    
  })
}

/* 
  保存：将当前详情保存至后台
    that: 当前函数调用时的上下文
    url: 后台地址
    obj: 需传给后台的参数
    callback: 成功回调
*/
Common.edit = function(that,url,obj,callback){
  that.$http.post(url, qs.stringify(obj)).then(response => {
    console.log(response.data);
    if(response.data.code===0){
      that.$Message.success('保存成功！即将跳转页面...')
      setTimeout(()=>{
        callback()
      },1000)
    }else{
      that.$Message.error('保存失败！' + response.data.info)
    }
    
  })
}

/* 
  删除：根据参数删除当前行内容
    that: 当前函数调用时的上下文
    url: 后台地址
    obj: 需传给后台的参数
    callback: 成功回调
*/
Common.remove = function(that, url, obj, callback) {
  that.$Modal.confirm({
    title: '请确认',
    content: '确认删除记录？',
    onOk: () => {
      that.$http.post(url, qs.stringify(obj)).then(response => {
        // console.log(response.data);
        if (response.data.code === 0) {
          that.$Message.success('删除成功！正在刷新页面...')
          callback()
        } else {
          that.$Message.error('删除失败！' + response.data.info)
        }
      })
    }
  })
}

/* 
  批量删除：根据参数删除选中的多行内容
    that: 当前函数调用时的上下文
    IDS: 数组，需要删除的内容的集合
    url: 后台地址s
    obj: 需传给后台的参数
    callback: 成功回调
*/
Common.batchRemove = function(that, IDS, url, obj, callback) {
  if (IDS.length == 0) {
    that.$Message.warning('没有被选中的条目！')
  } else {
    that.$Modal.confirm({
      title: '请确认',
      content: '确认删除记录？',
      onOk: () => {
        that.$http.post(url, qs.stringify(obj)).then(response => {
          // console.log(response.data);
          if (response.data.code === 0) {
            that.$Message.success('批量删除成功！正在刷新页面...')
            callback()
          } else {
            that.$Message.error('批量删除失败！' + response.data.info)
          }
        })
      }
    })
  }
}

export default Common



