<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的受检者</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的受检者</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
        <!--<a class="ivu-btn ivu-btn-primary" href="http://www-dev.basicsmanage.com/php/modal.xlsx" download="filename">下载模板</a>
        <Upload action="" :before-upload="handleUpload">
          <Button type="primary">批量上传</Button>
        </Upload>
        <div v-if="file !== null">Upload file: {{ file.name }} 
          <Button type="text" @click="upload" :loading="loadingStatus">{{ loadingStatus ? '正在上传' : '点击上传' }}
          </Button>
        </div>-->
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的受检者</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="姓" prop="LastName">
            <Input v-model="addForm.LastName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="中间名">
            <Input v-model="addForm.MiddleName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="名" prop="FirstName">
            <Input v-model="addForm.FirstName" placeholder="请输入受检者姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="出生日期" prop="DateOfBirth">
            <DatePicker @on-change="handleChange" format="yyyy-MM-dd" :options="DataPickerOption" placement="bottom-start" placeholder="请选择出生日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="性别" prop="Gender">
            <RadioGroup v-model="addForm.Gender" placeholder="请选择性别" style="width: 280px">
              <Radio label="m">男</Radio>
              <Radio label="f">女</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="证件类型" prop="IDType">
            <RadioGroup v-model="addForm.IDType" placeholder="请选择证件类型" style="width: 280px">
              <Radio label="二代身份证">二代身份证</Radio>
              <Radio label="护照">护照</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="证件号码" prop="IDNumber">
            <Input v-model="addForm.IDNumber" placeholder="请输入证件号码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="手机号码" prop="PhoneNumber">
            <Input v-model="addForm.PhoneNumber" placeholder="请输入手机号码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="邮箱地址">
            <Input v-model="addForm.Efront" placeholder="请输入邮箱名" style="width: 280px">
              <Select v-model="addForm.Eback" slot="append"  placeholder="请选择邮箱后缀" style="width: 120px">
                <Option value="@163.com">@163.com</Option>
                <Option value="@qq.com">@qq.com</Option>
                <Option value="@sina.com">@sina.com</Option>
              </Select>
            </Input>
          </FormItem>
          <FormItem label="家族史">
            <Input v-model="addForm.FamilyHistory" type="textarea" placeholder="请输入家族史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        // file: null,
        // loadingStatus: false,
        addForm: {
          LastName:'',
          MiddleName:'',
          FirstName:'',
          DateOfBirth:'',
          Gender:'m',
          IDType:'二代身份证',
          IDNumber:'',
          PhoneNumber:'',
          Efront:'',
          Eback:'@163.com',
          Email:'',
          FamilyHistory:''
        },
        rule:{
          LastName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          FirstName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DateOfBirth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Gender:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          IDType:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          IDNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PhoneNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      // download(){
      //   window.open("http://www-dev.basicsmanage.com/php/modal.xls","_blank")
      // },
      // handleUpload (file) {
      //   this.file = file;
      //   return false;
      // },
      // upload () {
      //   this.loadingStatus = true;
      //   setTimeout(() => {
      //     this.file = null;
      //     this.loadingStatus = false;
      //     this.$Message.success('Success')
      //   }, 1500);
      // },
      handleChange(date){
        // console.log(date);
        this.addForm.DateOfBirth = date;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //判断Email是否应为空
            if(this.addForm.Efront.trim()!==''){
              this.addForm.Email = this.addForm.Efront.trim()+this.addForm.Eback.trim();
            }
            let obj = {};
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/subject/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    .ivu-upload{
      display:inline-block;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


