<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browlibrary' + libID">查看文库详情</BreadcrumbItem>
      <BreadcrumbItem>添加新的变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的变异</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="addForm.LibID" placeholder="请输入文库编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Select v-model="addForm.ChrName" placeholder="请选择染色体名称" style="width: 280px">
              <Option v-for="(item,index) in chrArr":key="index" :value="item.ChrName">{{item.ChrName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="染色体位置" prop="Position">
            <Input v-model="addForm.Position" placeholder="请输入染色体位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="addForm.Symbol" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本">
            <Input v-model="addForm.transcript" placeholder="请输入转录本" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF序列">
            <Input v-model="addForm.Ref" placeholder="请输入REF序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="addForm.Alt" placeholder="请输入ALT序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Select v-model="addForm.VariantType" placeholder="请选择变异类型" style="width: 280px">
              <Option v-for="(item,index) in variantTypeArr":key="index" :value="item.VariantTypeID">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="基因型">
              <Input v-model="addForm.Genotype" placeholder="请输入基因型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因频率">
              <Input v-model="addForm.MinorAlleleFraction" placeholder="请输入基因频率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="测序深度">
              <Input v-model="addForm.DepthOfCoverage" placeholder="请输入测序深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="突变结果">
              <Input v-model="addForm.Consequence" placeholder="请输入突变结果" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.exon_intron" placeholder="请输入exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron位置">
              <Input v-model="addForm.Location" placeholder="请输入exon/intron位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="addForm.HGVSp" placeholder="请输入HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="addForm.HGVSc" placeholder="请输入HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="addForm.rsID" placeholder="请输入rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="addForm.Clin_Sig" placeholder="请输入临床意义" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="标记">
            <Input v-model="addForm.Flag" placeholder="请输入标记" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="影响">
            <Input v-model="addForm.Impact" placeholder="请输入影响" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/datainterface/getVariantByType.php",{},(response)=>{
        this.variantTypeArr = response.data
      })
      this.$Common.list(this,"/php/datainterface/getChr.php",{},(response)=>{
        this.chrArr = response.data
      })
    },
    data() {
      return {
        libID:location.search.split('=')[1],
        // symbolArr:[],
        // symbolData:[],
        variantTypeArr:[],
        chrArr:[],
        addForm: {
          LibID:location.search.split('=')[1],
          ChrName:'chr1',
          Position:'111',
          Symbol:'ABCB1',
          transcript:'111',
          Ref:'111',
          Alt:'111',
          VariantType:'1',
          Genotype:'111',
          MinorAlleleFraction:'111',
          DepthOfCoverage:'111',
          Consequence:'111',
          exon_intron:'111',
          Location:'111',
          HGVSp:'111',
          HGVSc:'111',
          rsID:'111',
          Clin_Sig:'111',
          Flag:'111',
          Impact:'111'
        },
        rule:{
          LibID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Position:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Symbol:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/snp_indel/add.php",obj,()=>{
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


