<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/user">用户管理</BreadcrumbItem>
      <BreadcrumbItem>修改密码</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="旧密码" prop="oldPwd">
            <Input type="password" v-model="editForm.oldPwd" placeholder="请输入旧密码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="新密码" prop="newPwd">
            <Input type="password" v-model="editForm.newPwd" placeholder="请输入新密码" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="确认密码" prop="confirmPwd">
            <Input type="password" v-model="editForm.confirmPwd" placeholder="请再次输入新密码" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      const validatePass=(rule, value, callback)=>{
        if(value===''){
          callback(new Error('请输入旧密码！'));
        }else{
          //发送请求验证密码是否正确
          callback();
        }
      };
      const validateNewPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入新密码！密码长度不能小于6位'));
        } else {
          if(value.trim().length<6){
            callback(new Error('密码长度不能小于6位,字符中不能出现空格'));
          }else{
            if (this.editForm.confirmPwd !== '') {
              // 对第二个密码框单独验证
              this.$refs.editForm.validateField('confirmPwd');
            }
          }
          callback();
        }
      };
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入新密码！'));
        } else if (value !== this.editForm.newPwd) {
          callback(new Error('两次新密码输入不一致！请重新输入'));
        } else {
          callback();
        }
      };
      return {
        editForm:{
          oldPwd:'',
          newPwd:'',
          confirmPwd:''
        },
        rule:{
          oldPwd:[
            { validator: validatePass, trigger: 'blur' }
          ],
          newPwd: [
            { validator: validateNewPass, trigger: 'blur' }
          ],
          confirmPwd: [
            { validator: validatePassCheck, trigger: 'blur' }
          ],
        }
      }
    },
    methods: {
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/user")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    .resetPwdBtn{
      color:@themeColor;
      &:hover{
        text-decoration:underline;
      }
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


