<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/biospeciemens">样本管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="bioRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm"  :inline="true" :model="browForm" label-position="right" :label-width="120">
         <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="browForm.LatestUpdateDate" readonly placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子诊断编号">
            <Input v-model="browForm.MolDiagnosisID" readonly placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="病理编号">
            <Input v-model="browForm.PathologicNumber" readonly placeholder="请输入病理编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本原始编号">
            <Input v-model="browForm.SampleOriID" readonly placeholder="请输入样本原始编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本公司编号">
            <Input v-model="browForm.SampleSGIID" readonly placeholder="请输入样本公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本类型">
            <Input v-model="browForm.SampleType" readonly placeholder="请选择样本类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样部位">
            <Input v-model="browForm.Site" readonly placeholder="请输入取样部位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样日期">
            <DatePicker v-model="browForm.SamplingDate" show-week-numbers placement="bottom-start" readonly placeholder="请选择取样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期">
            <DatePicker v-model="browForm.ReceivedDate" show-week-numbers placement="bottom-start" readonly placeholder="请选择收样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
            <Input v-model="browForm.RecipientsID" readonly placeholder="请输入收样人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
            <Input v-model="browForm.IsTumorCell" readonly placeholder="请选择是否为肿瘤细胞" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="browForm.TumorCellContent" readonly placeholder="请输入肿瘤细胞含量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本状态">
            <Input v-model="browForm.Status" readonly placeholder="请输入样本状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="browForm.Quantity" readonly placeholder="请输入样本量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量单位">
            <Input v-model="browForm.Unit" readonly placeholder="请输入样本量单位" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addNucleicAcid">添加新核酸</Button>
          </div>
          <div class="chromoListcontainer">
            <Table stripe border height="200" :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        this.$Common.list(this,"/php/biospecimens/index.php",{BiospeciemensID:this.biospeciemensID},(response)=>{
          this.browForm = response.data.data[0]
          resolve()
        })
      }).then(()=>{
        this.handlePage(this.pageNum)
      })
    },
    data() {
      return {
        biospeciemensID:location.search.split('=')[1],
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"核酸编号",
            key:'NucleicAcidID',
            align: 'center'
          },
          {
            title:"核酸公司编号",
            key:'NucleicAcidSGIID',
            align: 'center'
          },
          {
            title:"核酸提取用量及单位",
            key:'NAExtractionAmount',
            align: 'center'
          },
          {
            title:"核酸提取日期",
            key:'NAExtractionDate',
            align: 'center'
          },
          {
            title:"核酸提取人",
            key:'NAExtractionManipulatorID',
            align: 'center'
          },
          {
            title:"核酸体积及单位",
            key:'NAVolume',
            align: 'center'
          },
          {
            title:"核酸浓度及单位",
            key:'NAConc',
            align: 'center'
          },
          {
            title:"核酸总量及单位",
            key:'NAAmount',
            align: 'center'
          },
          {
            title:"核酸质量",
            key:'NAQuality',
            align: 'center'
          },
          {
            title:"核酸类型",
            key:'NAType',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.NucleicAcidID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.NucleicAcidID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.NucleicAcidID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      bioRemove(){
        this.$Common.remove(this,"/php/biospecimens/del.php",{BiospeciemensID:[this.biospeciemensID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })
      },
      handleEdit(){
        this.$router.push("/editbiospeciemens?biospeciemensid=" + this.biospeciemensID)
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
      addNucleicAcid(){
        this.$router.push("/addnucleicacid?biospeciemensid=" + this.biospeciemensID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/brownucleicacid?nucleicacidid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editnucleicacid?nucleicacidid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/nucleicacids/del.php",{NucleicAcidID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.NucleicAcidID)
        })
        this.$Common.batchRemove(this,IDS,"/php/nucleicacids/del.php",{NucleicAcidID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        let obj = {}
        obj.BiospeciemensID = this.biospeciemensID;
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .chromoListcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:5px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding: 10px;
      }
    }
  }
}
</style>


