<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpanel?panelid=' + this.panelID">查看Panel</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpanelrec?panelrecid='+ this.panelRecID">查看Panel升级记录</BreadcrumbItem>
      <BreadcrumbItem>添加新的基因</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的基因</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的基因</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="gene symbol" prop="Symbol">
            <AutoComplete v-model="addForm.Symbol" @on-search="searchSymbol" @on-select="selectSymbol" placeholder="请输入基因名称" style="width:280px">
              <Option v-for="(item,index) in symbolData" :value="item.Symbol" :key="index">{{item.Symbol}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getGeneBySymbol.php",qs.stringify({Symbol:''})).then(response=>{
        // console.log(response.data);
        this.symbolArr = response.data;
        this.symbolData = this.symbolArr.filter(item=>{
          return item.firstName!=='root'&&item.firstName!=='browser'
        })
      })
    },
    data() {
      return {
        panelID:location.search.split("&")[0].split("=")[1],
        panelRecID:location.search.split('&')[1].split("=")[1],
        symbolArr:[],
        symbolData:[],
        addForm: {
          GeneID:'',
          Symbol:''
        },
        rule:{
          Symbol:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchSymbol(value){
        this.$http.post("/php/datainterface/getGeneBySymbol.php",qs.stringify({Symbol:value})).then(response=>{
          // console.log(response.data);
          this.symbolArr = response.data;
          this.symbolData = this.symbolArr
        })
      },
      selectSymbol(value){
        // console.log(value);
        this.symbolData = this.symbolArr.filter(item=>{
          return item.Symbol===value
        })
        this.addForm.Symbol = value
        this.addForm.GeneID = this.symbolData[0].GeneID
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              let obj = {}
              obj.PanelUpdateRecordID = this.panelRecID.trim();
              obj.GeneID = this.addForm.GeneID;
              this.$Common.add(this,"/php/panel/panelrecord/memgene/geneadd.php",obj,()=>{
                this.$router.push("/browpanelrec?panelrecid=" + this.panelRecID)
              })
              // this.$http.post("/php/panel/panelrecord/memgene/geneadd.php",qs.stringify(obj)).then(response=>{
              //   // console.log(response.data);
              //   if(response.data.code===0){
              //     this.$Message.success("添加成功！即将跳转页面...")
              //     setTimeout(()=>{
              //       this.$router.push("/browpanelrec?panelrecid=" + this.panelRecID)
              //     },1000)
              //   }else{
              //     this.$Message.error("添加失败！" + response.data.info)
              //   }
                
              // })
            }
        })
      },
      backList(){
        this.$router.push("/browpanelrec?panelrecid=" + this.panelRecID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


