<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>核酸管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="核酸公司编号">
            <Input v-model="searchForm.NucleicAcidSGIID" placeholder="请输入核酸公司编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量(范围)">
            <Input v-model="searchForm.NAExtractionAmount" placeholder="请选择核酸提取用量(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸提取人">
            <Input v-model="searchForm.NAExtractionManipulatorID" placeholder="请输入核酸提取人" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期（起始时间段）">
            <DatePicker type="daterange" v-model="searchForm.NAExtractionDate" show-week-numbers placement="bottom-start" placeholder="请选择核酸提取日期（起始时间段）" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="核酸体积(范围)">
            <Input v-model="searchForm.NAVolume" placeholder="请输入核酸体积(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸浓度(范围)">
              <Input v-model="searchForm.NAConc" placeholder="请选择核酸浓度(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸总量(范围)">
            <Input v-model="searchForm.NAAmount" placeholder="请输入核酸总量(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
              <Select v-model="searchForm.NAQuality" placeholder="请选择核酸质量" style="width: 162px">
                <Option value="好">好</Option>
                <Option value="中">中</Option>
                <Option value="差">差</Option>
              </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="searchForm.Status" placeholder="请选择状态" style="width: 162px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNucleicAcid">添加新核酸提取</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-NAAmount show-sizer :NAAmount="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)

      this.handlePage(this.pageNum)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          NucleicAcidSGIID:'',
          NAExtractionAmount:'',
          NAExtractionManipulatorID:'',
          NAExtractionDate:'',
          NAVolume:'',
          NAConc:'',
          NAAmount:'',
          NAQuality:'',
          Status:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"核酸编号",
            key:'NucleicAcidID',
            align: 'center'
          },
          {
            title:"样本编号",
            key:'BiospeciemensID',
            align: 'center'
          },
          {
            title:"核酸公司编号",
            key:'NucleicAcidSGIID',
            align: 'center'
          },
          {
            title:"核酸提取用量及单位",
            key:'NAExtractionAmount',
            align: 'center'
          },
          {
            title:"核酸提取日期",
            key:'NAExtractionDate',
            align: 'center'
          },
          {
            title:"核酸提取人",
            key:'NAExtractionManipulatorID',
            align: 'center'
          },
          {
            title:"核酸体积及单位",
            key:'NAVolume',
            align: 'center'
          },
          {
            title:"核酸浓度及单位",
            key:'NAConc',
            align: 'center'
          },
          {
            title:"核酸总量及单位",
            key:'NAAmount',
            align: 'center'
          },
          {
            title:"核酸质量",
            key:'NAQuality',
            align: 'center'
          },
          {
            title:"核酸类型",
            key:'NAType',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.NucleicAcidID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.NucleicAcidID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.NucleicAcidID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.handlePage(this.pageNum)
      },
      addNucleicAcid(){
        this.$router.push("/addnucleicacid")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/brownucleicacid?nucleicacidid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editnucleicacid?nucleicacidid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/nucleicacids/del.php",{NucleicAcidID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })  
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.NucleicAcidID)
        })
        this.$Common.batchRemove(this,IDS,"/php/nucleicacids/del.php",{NucleicAcidID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        for(var key in this.searchForm){
          if(key!=='NAExtractionDate'){
            obj[key]=this.searchForm[key].trim()
          }else{
            //判断日期是不是由有值变为空
            this.searchForm[key][0]===''?obj[key]=[]:obj[key]=this.searchForm[key]
          }
        }
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$Common.list(this,"/php/nucleicacids/index.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


