<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>文库管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="文库公司编号">
            <Input v-model="searchForm.LibSGIID" placeholder="请输入文库公司编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="searchForm.LibConsAmount" placeholder="请选择文库提取用量(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库构建人">
              <Input v-model="searchForm.LibConsManipulatorID" placeholder="请输入文库提取人" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库构建日期">
            <DatePicker type="daterange" v-model="searchForm.LibConsDate" show-week-numbers placement="bottom-start" placeholder="请选择文库提取日期（起始时间段）" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="文库体积(范围)">
            <Input v-model="searchForm.LibVolume" placeholder="请输入文库体积(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库浓度(范围)">
              <Input v-model="searchForm.LibConc" placeholder="请选择文库浓度(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库总量(范围)">
            <Input v-model="searchForm.LibAmount" placeholder="请输入文库总量(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小(范围)">
            <Input v-model="searchForm.LibInsSize" placeholder="文库插入片段大小(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库质量">
            <Select v-model="searchForm.LibQuality" placeholder="请选择文库质量" style="width: 162px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="差">差</Option>
            </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="searchForm.Status" placeholder="请选择状态" style="width: 162px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addLib">添加新文库</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-LibAmount show-sizer :LibAmount="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      
      this.handlePage(this.pageNum)
    },
    data() {
      return {
        searchForm:{
          LibSGIID:'',
          LibConsAmount:'',
          LibConsManipulatorID:'',
          LibConsDate:'',
          LibVolume:'',
          LibConc:'',
          LibAmount:'',
          LibInsSize:'',
          LibQuality:'',
          Status:''//数字
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"文库编号",
            key:'LibID',
            align: 'center'
          },
          {
            title:"核酸编号",
            key:'NucleicAcidID',
            align: 'center'
          },
          {
            title:"文库公司编号",
            key:'LibSGIID',
            align: 'center'
          },
          {
            title:"文库构建用量及单位",
            key:'LibConsAmount',
            align: 'center'
          },
          {
            title:"文库构建日期",
            key:'LibConsDate',
            align: 'center'
          },
          {
            title:"文库构建人",
            key:'LibConsManipulator',
            align: 'center'
          },
          {
            title:"文库质量",
            key:'LibQuality',
            align: 'center'
          },
          {
            title:"文库体积及单位",
            key:'LibVolume',
            align: 'center'
          },
          {
            title:"文库浓度及单位",
            key:'LibConc',
            align: 'center'
          },
          {
            title:"文库总量及单位",
            key:'LibAmount',
            align: 'center'
          },
          {
            title:"文库插入片段大小",
            key:'LibInsSize',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.LibID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.LibID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.LibID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addLib(){
        this.$router.push("/addlibrary")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browlibrary?libid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editlibrary?libid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/library/del.php",{LibID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })   
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.LibID)
        })
        this.$Common.batchRemove(this,IDS,"/php/library/del.php",{LibID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        for(var key in this.searchForm){
          if(key!=='LibConsDate'){
            obj[key]=this.searchForm[key].trim()
          }else{
            //判断日期是不是由有值变为空
            this.searchForm[key][0]===''?obj[key]=[]:obj[key]=this.searchForm[key]
          }
        }
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$Common.list(this,"/php/library/edit.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


