<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpanel?panelid=' + this.panelID">查看panel</BreadcrumbItem>
      <BreadcrumbItem>添加新的Panel升级记录</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的Panel升级记录</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的Panel升级记录</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="Panel项目编号" prop="ProjectID">
            <Input v-model="addForm.ProjectID" :readonly="addForm.ProjectID!==''" placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号(0-99.99)" prop="Version">
            <Input type="text" v-model="addForm.Version" placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel方法" prop="PanelMethod">
            <Input v-model="addForm.PanelMethod" placeholder="请输入Panel方法" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel状态" prop="PanelStatus">
            <Select v-model="addForm.PanelStatus"  placeholder="请输入Panel状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="Panel启动日期" prop="PanelLanchDate">
            <DatePicker @on-change="changeLanchDate" show-week-numbers placement="bottom-start" placeholder="请选择Panel启动日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel结束日期" prop="PanelCompleteDate">
            <DatePicker @on-change="changeCompleteDate" show-week-numbers placement="bottom-start" placeholder="请选择Panel结束日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel描述">
            <Input type="textarea" v-model="addForm.PanelDescription" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/panel/index.php",{PanelID:this.panelID},(response)=>{
        this.addForm.PanelID = response.data.data[0].PanelID
        this.addForm.ProjectID = response.data.data[0].ProjectID
      })
    },
    data() {
      // const validateVersion = (rule, value, callback) => {
      //   if(isNaN(value)){
      //     callback(new Error('请输入有效数字，范围在0-99.99之间！'))
      //   }else if (value < 0) {
      //     callback(new Error('版本号范围在0-99.99之间！请重新输入'));
      //   } else if (value >99.99) {
      //     callback(new Error('版本号范围在0-99.99之间！请重新输入'));
      //   }else {
      //     callback();
      //   }
      // };
      const validateStartDate = (rule, value, callback) => {
        if(this.addForm.PanelCompleteDate){
          this.$refs.addForm.validateField('PanelCompleteDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.addForm.PanelLanchDate){
          if(value<this.addForm.PanelLanchDate){
            callback(new Error('结束日期不能早于启动日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        panelID:location.search.split('=')[1],
        addForm: {
          PanelID:'',
          ProjectID:'',
          Version:'',
          PanelMethod:'',
          PanelStatus:'',
          PanelLanchDate:'',
          PanelCompleteDate:'',
          PanelDescription:''
        },
        rule:{
          ProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            { required: true, message: '不能为空', trigger: 'blur' },
            // {
            //   validator: validateVersion, trigger: 'blur'
            // }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelLanchDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateStartDate, trigger: 'blur,change'}
          ],
          PanelCompleteDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateEndDate, trigger: 'blur,change'}            
          ]
        }
      }
    },
    methods: {
      changeLanchDate(date){
        this.addForm.PanelLanchDate = date
      },
      changeCompleteDate(date){
        this.addForm.PanelCompleteDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              let obj = {}
              for(var key in this.addForm){
                obj[key] = this.addForm[key].trim()
              }
              // console.log(obj);
              this.$Common.add(this,"/php/panel/panelrecord/add.php",obj,()=>{
                this.$router.push("/browpanel?panelid=" + this.addForm.PanelID)
              })
            }
        })
      },
      backList(){
        this.$router.push("/browpanel?panelid=" + this.panelID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


