<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">Panel管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpipeline?pipelineid=' + this.pipelineID">查看pipeline</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpipelinerec?pipelinerecid=' + this.pipelineRecID">查看Pipeline升级记录</BreadcrumbItem>
      <BreadcrumbItem>添加新的组员</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的组员</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的组员</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="E-mail" prop="Email">
            <AutoComplete v-model="addForm.Email" @on-search="searchEmail" @on-select="selectEmail" placeholder="请输入Email" style="width:280px">
              <Option v-for="(item,index) in emailData" :value="item.Email" :key="index">{{item.Email}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="姓" prop="LastName">
            <Input v-model="addForm.LastName" disabled placeholder="输入Email后自动填充" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="中间名">
            <Input v-model="addForm.MiddleName" disabled placeholder="输入Email后自动填充" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="名" prop="FirstName">
            <Input v-model="addForm.FirstName" disabled placeholder="输入Email后自动填充" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="角色">
          <!--<Input v-model="addForm.Role" placeholder="请选择角色" style="width: 280px"></Input>-->
            <Select v-model="addForm.Role"  placeholder="请选择角色" style="width: 280px">
              <Option value="角色1">角色1</Option>
              <Option value="角色2">角色2</Option>
              <Option value="角色3">角色3</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeByEmail.php",qs.stringify({Email:''})).then(response=>{
        // console.log(response.data);
        this.emailArr = response.data;
        this.emailData = this.emailArr.filter(item=>{
          return item.firstName!=='root'&&item.firstName!=='browser'
        })
      })
    },
    data() {
      return {
        pipelineID:location.search.split("&")[0].split("=")[1],
        pipelineRecID:location.search.split('&')[1].split("=")[1],
        emailArr:[],
        emailData:[],
        addForm: {
          EmployeeID:'',
          Email:'',
          LastName:'',
          MiddleName:'',
          FirstName:'',
          Role:'',
        },
        rule:{
          Email:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LastName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          FirstName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchEmail(value){
        this.$http.post("/php/datainterface/getEmployeeByEmail.php",qs.stringify({Email:value})).then(response=>{
          // console.log(response.data);
          this.emailArr = response.data;
          this.emailData = this.emailArr.filter(item=>{
            return item.firstName!=='root'&&item.firstName!=='browser'
          })
        })
      },
      selectEmail(value){
        // console.log(value);
        this.emailData = this.emailArr.filter(item=>{
          return item.Email===value
        })
        this.addForm.Email = value;
        this.addForm.EmployeeID = this.emailData[0].EmployeeID;
        this.addForm.LastName = this.emailData[0].LastName;
        this.addForm.FirstName = this.emailData[0].FirstName;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            obj.PipelineUpdateRecordID = this.pipelineRecID.trim();
            obj.EmployeeID = this.addForm.EmployeeID;
            obj.Role = this.addForm.Role.trim()
            this.$Common.add(this,"/php/pipeline/pipelinerecord/members/add.php",obj,()=>{
              this.$router.push("/browpipelinerec?pipelinerecid=" + this.pipelineRecID)
            })
          }
        })
      },
      backList(){
        this.$router.push("/browpipelinerec?pipelinerecid=" + this.pipelineRecID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


