<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browproduct?productid='+ this.browForm.ProductID">查看产品</BreadcrumbItem>
      <BreadcrumbItem>查看产品升级记录详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="productRecRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="browForm.CreateDate" readonly placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="browForm.LatestUpdateDate" readonly placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="browForm.ProductID" readonly placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品升级记录编号">
            <Input v-model="browForm.ProductUpdateRecordID" readonly placeholder="请输入产品升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="productName_cn" readonly placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号">
            <Input v-model="browForm.Version" readonly placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态">
            <Input v-model="browForm.ProductStatus" readonly placeholder="请输入产品状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品上线日期">
            <DatePicker v-model="browForm.ProductOnlineDate" readonly placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期">
            <DatePicker v-model="browForm.ProductOfflineDate" readonly placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型">
            <Input v-model="browForm.VariantTypeID" readonly placeholder="请输入产品检测的变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="browForm.ProductDescription" readonly placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="teamCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addTeaNumber">添加新组员</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border :loading="loading" height="200" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="geneCon">
            <Tag type="dot" closable color="blue" v-for="(item,index) in geneArr" :key="index" :name="item.ProductGeneID" @on-close="handleClose">{{item.Symbol}}</Tag>
            <Button size="small" type="primary" @click="addGene" icon="plus-round">添加基因</Button>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        this.$http.post("/php/product/productrecord/index.php",qs.stringify({ProductUpdateRecordID:this.productUpdateRecordID})).then(response=>{
          // console.log(response.data);
          this.browForm = response.data.data[0]
          resolve()
        })
      }).then(resolve=>{
        //获取产品中文名称
        this.$http.post("/php/product/index.php",qs.stringify({ProductID:this.browForm.ProductID})).then(response=>{
          // console.log(response.data);
          this.productName_cn = response.data.data[0].ProductName_cn;
        })

        // 获取组员列表
        this.handlePage(this.pageNum)

        // 获取基因列表
        this.getGene()
      })
    },
    data() {
      return {
        productUpdateRecordID:location.search.split('=')[1],
        productName_cn:'',
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"组员姓名",
            key:'Name',
            align: 'center'
          },
          {
            title:"E-mail",
            key:'Email',
            align: 'center'
          },
          {
            title:"角色",
            key:'Role',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ProductMemberID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10,
        geneArr:[]
      }
    },
    methods: {
      productRecRemove(){
        this.$Common.remove(this,"/php/product/productrecord/del.php",{ProductUpdateRecordID:[this.productUpdateRecordID]},()=>{
          setTimeout(()=>{
            this.$router.push("/browproduct?productid=" + this.browForm.ProductID)
          },1000)
        })
      },
      handleEdit(){
        this.$router.push("/editproductrec?producrectid=" + this.productUpdateRecordID)
      },
      backList(){
        this.$router.push("/browproduct?productid=" + this.browForm.ProductID)
      },
      addTeaNumber(){
        //将当前受检者的ID传入
        this.$router.push("/addteamnumber2?productid=" + this.browForm.ProductID + "&producrectid=" + this.productUpdateRecordID)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/product/productrecord/memgene/memdel.php",{ProductMemberID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ProductMemberID)
        })
        this.$Common.batchRemove(this,IDS,"/php/product/productrecord/memgene/memdel.php",{ProductMemberID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        this.$http.post("/php/product/productrecord/memgene/memindex.php",qs.stringify({
          ProductUpdateRecordID:this.productUpdateRecordID,
          pageNum:this.pageNum,
          pageSize:this.pageSize
        })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data
        this.TotalNum = Number(response.data.num)
        this.loading = false;
      })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      },
      getGene(){
        this.$http.post("/php/product/productrecord/memgene/geneindex.php",qs.stringify({ProductUpdateRecordID:this.productUpdateRecordID})).then(response=>{
          // console.log(response.data);
          this.geneArr = response.data.data;
        })
      },
      handleClose(event, name){
        // console.log(event);
        // console.log(name);
        this.$Common.remove(this,"/php/product/productrecord/memgene/genedel.php",{ProductGeneID:[name]},()=>{
          this.getGene()
        })
      },
      addGene(){
        this.$router.push("/addgene2?productid=" + this.browForm.ProductID + "&producrectid=" + this.productUpdateRecordID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .teamCon{
      box-sizing:border-box;
      
    }
    .geneCon{
      padding:10px;
      min-height:100px;
      border:1px dashed #ccc;
      .ivu-tag{
        height:auto;
        margin: -2px 10px 5px;
        padding: 3px 18px;
      }
      .ivu-btn{
        min-height:38px;
        padding:0;
      }
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


