<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">信息分析流程管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的信息分析流程</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的信息分析流程</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="150">
          <FormItem label="信息分析流程项目编号" prop="PipelineProjectID">
            <Input v-model="addForm.PipelineProjectID" placeholder="请输入信息分析流程项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <AutoComplete v-model="addForm.ProductName_cn" @on-search="searchName" @on-select="selectName" placeholder="请先输入产品中文名称" style="width:280px">
              <Option v-for="(item,index) in productData" :value="item.ProductName_cn" :key="index">{{item.ProductName_cn}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="产品版本号" prop="Version">
            <AutoComplete v-model="addForm.Version" :disabled="versionStep" @on-search="searchVersion" @on-select="selectVersion" placeholder="再输入产品版本号" style="width:280px">
              <Option v-for="(item,index) in versionData" :value="item.Version" :key="index">{{item.Version}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/product/index.php",qs.stringify()).then(response=>{
        // console.log(response.data);
        this.productArr = response.data.data
        this.productData = response.data.data
      })
    },
    data() {
      return {
        productArr:[],
        productData:[],
        versionArr:[],
        versionData:[],
        versionStep:true,
        addForm: {
          PipelineProjectID:'',
          ProductName_cn:'',
          Version:'',
          ProductUpdateRecordID:''
        },
        rule:{
          PipelineProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchName(value){
        this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          this.productArr = response.data.data
          this.productData = response.data.data
        })
      },
      selectName(value){
        this.$http.post("/php/datainterface/getProductNameByNum.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          if(response.data.length==0){
            this.$Message.warning("该产品尚没有版本号！")
            return false;
          }
          this.versionArr = response.data
          this.versionData = response.data
          this.versionStep = false;
        })
      },
      searchVersion(value){
        this.versionData = this.versionArr.filter(item=>{
          return item.Version.indexOf(value)!==-1
        })
      },
      selectVersion(value){
        this.addForm.ProductUpdateRecordID = this.versionData.filter(item=>{
          return item.ProductName_cn===this.addForm.ProductName_cn&&item.Version===value
        })[0].ProductUpdateRecordID
        // console.log(this.addForm);
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Common.add(this,'/php/pipeline/add.php',{
              PipelineProjectID:this.addForm.PipelineProjectID.trim(),
              ProductUpdateRecordID:this.addForm.ProductUpdateRecordID.trim(),
            },()=>{
              this.$router.push("/pipeline")
            })
          }
        })
      },
      backList(){
        this.$router.push("/pipeline")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


