<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的Panel</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的Panel</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的Panel</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="Panel项目编号" prop="ProjectID">
            <Input v-model="addForm.ProjectID" placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称" prop="PanelName_cn">
            <Input v-model="addForm.PanelName_cn" placeholder="请输入Panel中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel英文名称" prop="PanelName_en">
            <Input v-model="addForm.PanelName_en" placeholder="请输入Panel英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          ProjectID:'',
          PanelName_cn:'',
          PanelName_en:'',
        },
        rule:{
          ProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/panel/add.php",obj,()=>{
              this.$router.push("/panel")
            })
          }
        })
      },
      backList(){
        this.$router.push("/panel")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


