<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpipeline?pipelineid='+ this.editForm.PipelineID">查看panel</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="160">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程编号">
            <Input v-model="editForm.PipelineID" disabled placeholder="请输入信息分析流程编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程更新记录编号">
            <Input v-model="editForm.PipelineUpdateRecordID" disabled placeholder="请输入信息分析流程更新记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程版本号" prop="PipelineVersion">
            <Input v-model="editForm.PipelineVersion" placeholder="请输入信息分析流程版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程中文名称" prop="PipelineName_cn">
            <Input v-model="editForm.PipelineName_cn" placeholder="请输入信息分析流程中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程英文名称" prop="PipelineName_en">
            <Input v-model="editForm.PipelineName_en" placeholder="请输入信息分析流程英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程状态" prop="PipelineStatus">
            <Select v-model="editForm.PipelineStatus"  placeholder="请输入Panel状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="信息分析流程上线日期" prop="PipelineOnlineDate">
            <DatePicker @on-change="changeLanchDate" :value="editForm.PipelineOnlineDate" show-week-numbers placement="bottom-start" placeholder="请选择信息分析流程上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程下线日期" prop="PipelineOfflineDate">
            <DatePicker @on-change="changeCompleteDate" :value="editForm.PipelineOfflineDate" show-week-numbers placement="bottom-start" placeholder="请选择信息分析流程下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程描述">
            <Input type="textarea" v-model="editForm.PipelineDescription" placeholder="请输入信息分析流程描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/pipeline/pipelinerecord/edit.php",qs.stringify({PipelineUpdateRecordID:this.pipelineUpdateRecordID})).then(response=>{
        // console.log(response.data);
        this.editForm = response.data[0]
      })
    },
    data() {
      const validateStartDate = (rule, value, callback) => {
        if(this.editForm.PipelineOfflineDate){
          this.$refs.editForm.validateField('PipelineOfflineDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.editForm.PipelineOnlineDate){
          if(value<this.editForm.PipelineOnlineDate){
            callback(new Error('下线日期不能早于上线日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        pipelineUpdateRecordID:location.search.split("=")[1],
        editForm:{},
        rule:{
          PipelineVersion:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineOnlineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineOfflineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      changeLanchDate(date){
        this.editForm.PipelineOnlineDate = date
      },
      changeCompleteDate(date){
        this.editForm.PipelineOfflineDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/pipeline/pipelinerecord/update.php",obj,()=>{
              this.$router.push("/browpipeline?pipelineid=" + this.editForm.PipelineID)
            })
          }
        })
      },
      backList(){
        this.$router.push("/browpipeline?pipelineid=" + this.editForm.PipelineID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


