<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="removePanel">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel项目编号" prop="ProjectID">
            <Input v-model="editForm.ProjectID" disabled placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称" prop="PanelName_cn">
            <Input v-model="editForm.PanelName_cn" placeholder="请输入Panel中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel英文名称" prop="PanelName_en">
            <Input v-model="editForm.PanelName_en" placeholder="请输入Panel英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/panel/index.php",qs.stringify({PanelID:this.panelID})).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        panelID:location.search.split('=')[1],
        editForm:{},
        rule:{
          ProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      removePanel(){
        this.$http.post("/php/panel/del.php",qs.stringify({PanelID:[this.panelID]})).then(response=>{
          if(response.data){
            this.$Message.success('删除成功！正在跳转页面...')
            setTimeout(()=>{
              this.$router.push("/panel")
            },1000)
          }else{
            this.$Message.success('删除失败！请稍后重试...')
          }
        })
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              obj[key]=this.editForm[key].trim()
            }
            this.$Common.edit(this,"/php/panel/edit.php",obj,()=>{
              this.$router.push("/panel")
            })
          }
        })
      },
      backList(){
        this.$router.push("/panel")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


