<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">信息分析流程管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="140">
          <FormItem label="创建日期">
            <DatePicker v-model="browForm.CreateDate" readonly show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="browForm.LatestUpdateDate" readonly show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程编号">
            <Input v-model="browForm.PipelineID" readonly placeholder="请输入信息分析流程编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程项目编号">
            <Input v-model="browForm.PipelineProjectID" readonly placeholder="请输入信息分析流程项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <Input v-model="browForm.ProductName_cn" placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号" prop="Version">
            <Input v-model="browForm.Version" placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addPipelineRec">添加新信息分析流程更新记录</Button>
          </div>
          <div class="listcontainer">
            <Table stripe border :loading="loading" height="200" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      //获取当前流程的详情
      this.$http.post('/php/pipeline/list.php',qs.stringify({PipelineID:this.pipelineID})).then(response=>{
        // console.log(response.data);
        this.browForm = response.data.data[0]
      })
      //获取记录列表
      this.handlePage(this.pageNum)
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        pipelineID:location.search.split('=')[1],
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"信息分析流程中文名称",
            key:'PipelineName_cn',
            align: 'center'
          },
          {
            title:"信息分析流程英文名称",
            key:'PipelineName_en',
            align: 'center'
          },
          {
            title:"信息分析流程版本号",
            key:'PipelineVersion',
            align: 'center'
          },
          {
            title:"信息分析流程状态",
            key:'PipelineStatus',
            align: 'center'
          },
          {
            title:"信息分析流程上线日期",
            key:'PipelineOnlineDate',
            align: 'center'
          },
          {
            title:"信息分析流程下线日期",
            key:'PipelineOfflineDate',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.PipelineUpdateRecordID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.PipelineUpdateRecordID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.PipelineUpdateRecordID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editpipeline?pipelineid=" + this.pipelineID)
      },
      backList(){
        this.$router.push("/pipeline")
      },
      addPipelineRec(){
        this.$router.push("/addpipelinerec?pipelineid=" + this.pipelineID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browpipelinerec?pipelinerecid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editpipelinerec?pipelinerecid=" + id)
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading=true;
        this.pageNum = val;
        this.$http.post('/php/pipeline/pipelinerecord/list.php',qs.stringify({
          PipelineID:this.pipelineID,
          pageNum:this.pageNum,
          pageSize:this.pageSize
        })).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


