<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary">查看文库</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browqc'">查看QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editqc'">编辑QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="qcRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="editForm.LibID" disabled placeholder="请输入文库编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="QC编号" prop="QCID">
            <Input v-model="editForm.QCID" disabled placeholder="请输入文库编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总碱基数" prop="TotalBases">
            <Input v-model="editForm.TotalBases" :disabled="currentPath=='browqc'" placeholder="请输入总碱基数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总序列数" prop="TotalReadNumber">
            <Input v-model="editForm.TotalReadNumber" :disabled="currentPath=='browqc'" placeholder="请输入总序列数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="重复率" prop="DuplicationRate">
            <Input v-model="editForm.DuplicationRate" :disabled="currentPath=='browqc'" placeholder="请输入重复率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="比对比率" prop="MappingRate">
              <Input v-model="editForm.MappingRate" :disabled="currentPath=='browqc'" placeholder="请输入比对比率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="序列长度" prop="ReadLength">
              <Input v-model="editForm.ReadLength" :disabled="currentPath=='browqc'" placeholder="请输入序列长度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="捕获效率" prop="CaptureEfficacy">
              <Input v-model="editForm.CaptureEfficacy" :disabled="currentPath=='browqc'" placeholder="请输入捕获效率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针平均深度" prop="ProbeMeanDepth">
              <Input v-model="editForm.ProbeMeanDepth" :disabled="currentPath=='browqc'" placeholder="请输入探针平均深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针中点深度" prop="ProbeMedianDepth">
            <Input v-model="editForm.ProbeMedianDepth" :disabled="currentPath=='browqc'" placeholder="请输入探针中点深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针均一性" prop="ProbeUniformity">
            <Input v-model="editForm.ProbeUniformity" :disabled="currentPath=='browqc'" placeholder="请输入探针均一性" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率" prop="DepthGT1Rate">
            <Input v-model="editForm.DepthGT1Rate" :disabled="currentPath=='browqc'" placeholder="请输入覆盖率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率≥30" prop="DepthGT30Rate">
            <Input v-model="editForm.DepthGT30Rate" :disabled="currentPath=='browqc'" placeholder="请输入覆盖率≥30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.Status" :disabled="currentPath=='browqc'" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browqc'">编辑</Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editqc'">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$Common.list(this,"/php/qc/edit.php",{QCID:this.qcID},(response)=>{
          this.editForm = response.data.data[0]
        })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        qcID:location.search.split('=')[1],
        editForm:{},
        rule:{
          LibID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          TotalBases:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          TotalReadNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DuplicationRate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          MappingRate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ReadLength:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          CaptureEfficacy:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeMeanDepth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeMedianDepth:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProbeUniformity:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DepthGT1Rate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          DepthGT30Rate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      qcRemove(){
        this.$Common.remove(this,"/php/qc/del.php",{QCID:[this.qcID]},()=>{
          this.qcHandlePage(this.qcPageNum)
          this.qcMultipleSelection = []
        })
      },
      handleEdit(){
        this.currentPath="editqc"
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/qc/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


