<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpanel?panelid=' + this.editForm.PanelID">查看panel</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="removePanelRec">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel项目编号">
            <Input v-model="ProjectID" disabled placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel升级记录编号">
            <Input v-model="editForm.PanelUpdateRecordID" disabled placeholder="请输入Panel升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号(0-99.99)" prop="Version">
            <Input type="text" v-model="editForm.Version" placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel方法" prop="PanelMethod">
            <Input v-model="editForm.PanelMethod"  placeholder="请输入Panel方法" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel状态" prop="PanelStatus">
            <Select v-model="editForm.PanelStatus"  placeholder="请输入Panel状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="Panel启动日期" prop="PanelLanchDate">
            <DatePicker :value="editForm.PanelLanchDate" @on-change="changeLanchDate" show-week-numbers placement="bottom-start" placeholder="请选择Panel启动日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel结束日期" prop="PanelCompleteDate">
            <DatePicker :value="editForm.PanelCompleteDate" @on-change="changeCompleteDate" show-week-numbers placement="bottom-start" placeholder="请选择Panel结束日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel描述">
            <Input type="textarea" v-model="editForm.PanelDescription"  placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/panel/panelrecord/index.php",qs.stringify({
        PanelUpdateRecordID:this.panelRecID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
        this.$http.post("/php/panel/index.php",qs.stringify({PanelID:this.editForm.PanelID})).then(response=>{
          this.ProjectID = response.data.data[0].ProjectID
        })
      })
    },
    data() {
      // const validateAge = (rule, value, callback) => {
      //   if(isNaN(value)){
      //     callback(new Error('请输入有效数字，范围在0-99.99之间！'))
      //   }else if (value < 0) {
      //     callback(new Error('版本号范围在0-99.99之间！请重新输入'));
      //   } else if (value >99.99) {
      //     callback(new Error('版本号范围在0-99.99之间！请重新输入'));
      //   }else {
      //     callback();
      //   }
      // };
      const validateStartDate = (rule, value, callback) => {
        if(this.editForm.PanelCompleteDate){
          this.$refs.editForm.validateField('PanelCompleteDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.editForm.PanelLanchDate){
          if(value<this.editForm.PanelLanchDate){
            callback(new Error('结束日期不能早于启动日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        panelRecID:location.search.split('=')[1],
        ProjectID:'',
        editForm:{
          PanelID:'',
          PanelUpdateRecordID:'',
          Version:'',
          PanelMethod:'',
          PanelStatus:'',
          PanelLanchDate:'',
          PanelCompleteDate:'',
          PanelDescription:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        rule:{
          ProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            { required: true, message: '不能为空', trigger: 'blur' },
            // {
            //   validator: validateAge, trigger: 'blur'
            // }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PanelLanchDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateStartDate, trigger: 'blur,change'}
          ],
          PanelCompleteDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateEndDate, trigger: 'blur,change'}            
          ]
        }
      }
    },
    methods: {
      removePanelRec(){
        this.$Common.remove(this,"/php/panel/panelrecord/del.php",{PanelUpdateRecordID:[this.panelRecID]},()=>{
          setTimeout(()=>{
              this.$router.push("/browpanel?panelid=" + this.editForm.PanelID)
            },1000)
        })
      },
      changeLanchDate(date){
        this.editForm.PanelLanchDate = date
      },
      changeCompleteDate(date){
        this.editForm.PanelCompleteDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=="CreateDate"&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,"/php/panel/panelrecord/edit.php",obj,()=>{
              this.$router.push("/browpanel?panelid=" + this.editForm.PanelID)
            })
          }
        })
      },
      backList(){
        this.$router.push("/browpanel?panelid=" + this.editForm.PanelID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


