<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="removePanel">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="browForm.LatestUpdateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel项目编号">
            <Input v-model="browForm.ProjectID" readonly placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称">
            <Input v-model="browForm.PanelName_cn" readonly placeholder="请输入Panel中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel英文名称">
            <Input v-model="browForm.PanelName_en" readonly placeholder="请输入Panel英文名称" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addPanelRec">添加新 Panel 升级记录</Button>
          </div>
          <div class="listcontainer">
            <Table stripe border :loading="loading" height="300" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" @click="batchRemove" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/panel/index.php",qs.stringify({PanelID:this.panelID})).then(response=>{
        // console.log(response.data);
        this.browForm = response.data.data[0]
      })
       this.$http.post("/php/panel/panelrecord/index.php",qs.stringify({PanelID:this.panelID})).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data
        this.TotalNum = response.data.num
        this.loading = false;
      })
    },
    data() {
      return {
        panelID:location.search.split('=')[1],
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Panel更新记录编号",
            key:'PanelUpdateRecordID',
            align: 'center'
          },
          {
            title:"Panel版本号",
            key:'Version',
            align: 'center'
          },
          {
            title:"Panel方法",
            key:'PanelMethod',
            align: 'center'
          },
          {
            title:"Panel状态",
            key:'PanelStatus',
            align: 'center'
          },
          {
            title:"Panel启动日期",
            key:'PanelLanchDate',
            align: 'center'
          },
          {
            title:"Panel完成日期",
            key:'PanelCompleteDate',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.PanelUpdateRecordID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.PanelUpdateRecordID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.PanelUpdateRecordID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editpanel?panelid=" + this.panelID)
      },
      removePanel(){
        this.$http.post("/php/panel/del.php",qs.stringify({PanelID:[this.panelID]})).then(response=>{
          if(response.data.code===0){
            this.$Message.success('删除成功！正在跳转页面...')
            setTimeout(()=>{
              this.$router.push("/panel")
            },1000)
          }else{
            this.$Message.success('删除失败！' + response.data.info)
          }
        })
      },
      backList(){
        this.$router.push("/panel")
      },
      addPanelRec(){
        this.$router.push("/addpanelrec?panelid=" + this.panelID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browpanelrec?panelrecid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editpanelrec?panelrecid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$http.post("/php/panel/panelrecord/del.php",qs.stringify({PanelUpdateRecordID:[id]})).then(response=>{
          // console.log(response.data);
          if(response.data.code===0){
            this.$Message.success('删除成功！正在刷新页面...');
            this.handlePage(this.pageNum)
            this.multipleSelection=[]
          }else{
            this.$Message.error('删除失败！' + response.data.info);
          }
        })  
      },
      batchRemove(){
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.PanelUpdateRecordID)
        })
        this.$http.post("/php/panel/panelrecord/del.php",qs.stringify({PanelUpdateRecordID:IDS})).then(response=>{
          if(response.data.code===0){
            this.$Message.success('批量删除成功！正在刷新页面...')
            this.handlePage(this.pageNum)
            this.multipleSelection = []
          }else{
            this.$Message.success('批量删除失败！' + response.data.info)
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading=true;
        this.pageNum = val;
        let obj = {}
        obj.PanelID = this.panelID;
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$http.post("/php/panel/panelrecord/index.php",qs.stringify(obj)).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data
        this.TotalNum = response.data.num
        this.loading = false;
      })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


