<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>分子诊断管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="姓名">
              <Input v-model="searchForm.SubjectName" placeholder="请输入姓名" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="年龄">
              <Input v-model="searchForm.Age" placeholder="请输入姓名" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="性别">
            <Select v-model="searchForm.Sexy" placeholder="请选择性别" style="width: 162px">
              <Option value="男">男</Option>
              <Option value="女">女</Option>
            </Select>
          </FormItem>
          <FormItem label="受检者编号">
              <Input v-model="searchForm.SubjectID" placeholder="请输入受检者编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="分子诊断产品">
              <Input v-model="searchForm.MolDiagnosisName" placeholder="请输入分子诊断产品" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="searchForm.Status" placeholder="请输入状态" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="daterange" v-model="searchForm.CreateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker type="daterange" v-model="searchForm.LatestUpdateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addMolDiagnosis">添加新分子诊断</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import expandTpl from './expandTpl';
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      
      this.handlePage(this.pageNum)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          SubjectName:'',
          Age:'',
          Sexy:'',
          SubjectID:'',
          MolDiagnosisName:'',
          Status:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"受检者编号",
            key:'SubjectID',
            align: 'center'
          },
          {
            title:"姓名",
            key:'Fullname',
            align: 'center'
          },
          {
            title:"年龄",
            key:'Age',
            align: 'center'
          },
          {
            title:"性别",
            key:'Gender',
            align: 'center'
          },
          {
            title:"分子诊断编号",
            key:'MolDiagnosisID',
            align: 'center'
          },
          {
            title:"分子诊断产品",
            key:'ProductName_cn',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center',
            render: (h, params) => {
              return h(expandTpl, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.MolDiagnosisID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.MolDiagnosisID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.MolDiagnosisID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.handlePage(this.pageNum)
      },
      addMolDiagnosis(){
        this.$router.push("/addmoldiagnosis")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browmoldiagnosis?moldiagnosisid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editmoldiagnosis?moldiagnosisid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$Common.remove(this,"/php/moldiagnosis/del.php",{MolDiagnosisID:[id]},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      batchRemove(){
        let IDS = []
        this.multipleSelection.forEach(item=>{
          IDS.push(item.MolDiagnosisID)
        })
        this.$Common.batchRemove(this,IDS,"/php/moldiagnosis/del.php",{MolDiagnosisID:IDS},()=>{
          this.handlePage(this.pageNum)
          this.multipleSelection = []
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading= true;
        this.pageNum = val;
        let obj = {}
        for(var key in this.searchForm){
          if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
            obj[key]=this.searchForm[key].trim()
          }else{
            //判断日期是不是由有值变为空
            this.searchForm[key][0]===''?obj[key]=[]:obj[key]=this.searchForm[key]
          }
        }
        obj.pageNum = this.pageNum
        obj.pageSize = this.pageSize
        this.$Common.list(this,'/php/moldiagnosis/edit.php',obj,(response)=>{
          this.tableData=response.data.data;
          this.TotalNum=Number(response.data.num);
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


