<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/biospeciemens">样本管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的样本</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的样本</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的样本</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="分子诊断编号" prop="MolDiagnosisID">
            <Input v-model="addForm.MolDiagnosisID" v-if="molDiagnosisID" disabled placeholder="请输入分子诊断编号" style="width: 280px"></Input>
            <AutoComplete v-model="addForm.MolDiagnosisID" v-if="!molDiagnosisID" @on-search="searchMolDiagnosisID" @on-select="selectMolDiagnosisID" placeholder="请输入分子诊断编号" style="width:280px">
                <Option v-for="(item,index) in molDiagnosisData" :value="item.MolDiagnosisID" :key="index">{{item.MolDiagnosisID}}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="病理编号" prop="PathologicNumber">
            <Input v-model="addForm.PathologicNumber" placeholder="请输入病理编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本原始编号" prop="SampleOriID">
            <Input v-model="addForm.SampleOriID" placeholder="请输入样本原始编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本公司编号" prop="SampleSGIID">
            <Input v-model="addForm.SampleSGIID" placeholder="请输入样本公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本类型" prop="SampleType">
            <Select v-model="addForm.SampleType" placeholder="请选择样本类型" style="width: 280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
            </Select>
          </FormItem>
          <FormItem label="取样部位" prop="Site">
            <Input v-model="addForm.Site" placeholder="请输入取样部位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样日期" prop="SamplingDate">
            <DatePicker type="date" @on-change="changeSamplingDate" show-week-numbers placement="bottom-start" placeholder="请选择取样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期" prop="ReceivedDate">
            <DatePicker type="date" @on-change="changeReceivedDate" show-week-numbers placement="bottom-start" placeholder="请选择收样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
            <Select v-model="addForm.RecipientsID" placeholder="请选择收样人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
            <Select v-model="addForm.IsTumorCell" placeholder="请选择是否为肿瘤细胞" style="width: 280px">
              <Option value="是">是</Option>
              <Option value="否">否</Option>
            </Select>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="addForm.TumorCellContent" placeholder="请输入肿瘤细胞含量（数字）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本状态">
            <Select v-model="addForm.Status" placeholder="请选择状态" style="width:280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="addForm.Quantity" placeholder="请输入样本量（数字）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量单位">
            <Input v-model="addForm.Unit" placeholder="请输入样本量单位" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
      })
      if(this.molDiagnosisID===undefined){
        let obj = {
          MolDiagnosisID:this.molDiagnosisID,
        }
        this.$Common.list(this,"/php/biospecimens/index.php",obj,(response)=>{
          this.molDiagnosisArr = response.data.data
          this.molDiagnosisData = response.data.data
        })
      }else{
        this.addForm.MolDiagnosisID = this.molDiagnosisID
      }
    },
    data() {
      // const validateSamplingDate = (rule, value, callback) => {
      //   if(this.addForm.ReceivedDate){
      //     this.$refs.addForm.validateReceivedDate('ReceivedDate');
      //   }
      //   callback()
      // };
      // const validateReceivedDate = (rule, value, callback) => {
      //   if(this.addForm.SamplingDate){
      //     if(value<this.addForm.SamplingDate){
      //       callback(new Error('收样日期不能早于取样日期！'))
      //     }else{
      //       callback()
      //     }
      //   }
      // };
      return {
        molDiagnosisID:location.search.split("=")[1],
        employeeData:[],
        molDiagnosisArr:[],
        molDiagnosisData:[],
        addForm: {
          MolDiagnosisID:'',
          PathologicNumber:'',
          SampleOriID:'',
          SampleSGIID:'',
          SampleType:'',
          Site:'',
          SamplingDate:'',
          ReceivedDate:'',
          RecipientsID:'',
          IsTumorCell:'',
          TumorCellContent:'',
          Status:'',
          Quantity:'',
          Unit:''
        },
        rule:{
          MolDiagnosisID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PathologicNumber:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleOriID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SampleType:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Site:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SamplingDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            // {
            //   validator: validateSamplingDate, trigger: 'blur,change'
            // }
          ],
          ReceivedDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            // {
            //   validator: validateReceivedDate, trigger: 'blur,change'
            // }
          ]
        }
      }
    },
    methods: {
      changeSamplingDate(date){
        this.addForm.SamplingDate = date
      },
      changeReceivedDate(date){
        this.addForm.ReceivedDate = date
      },
      searchMolDiagnosisID(value){
        let obj = {}
        obj.MolDiagnosisID = value
        this.$Common.list(this,"/php/biospecimens/index.php",obj,(response)=>{
          this.molDiagnosisArr = response.data.data
          this.molDiagnosisData = response.data.data
        })
      },
      selectMolDiagnosisID(value){
        this.molDiagnosisData = this.molDiagnosisArr.filter(item=>{
          return item.MolDiagnosisID === value
        })
        this.addForm.MolDiagnosisID = value
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            // console.log(this.addForm);
            let obj = {}
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            console.log(obj);
            this.$Common.add(this,"/php/biospecimens/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


