<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browproduct?productid=' + this.addForm.ProductID">查看产品</BreadcrumbItem>
      <BreadcrumbItem>添加新的产品升级记录</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的产品升级记录</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的产品升级记录</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="产品编号" prop="ProductID">
            <Input v-model="addForm.ProductID" disabled placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <Input v-model="addForm.ProductName_cn" disabled placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号(0-99.99)" prop="Version">
            <Input v-model="addForm.Version" placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态" prop="ProductStatus">
            <Select v-model="addForm.ProductStatus"  placeholder="请选择产品状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="产品上线日期" prop="ProductOnlineDate">
            <DatePicker @on-change="changeLanchDate" placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期" prop="ProductOfflineDate">
            <DatePicker @on-change="changeCompleteDate" placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型" prop="VariantTypeID">
            <Select v-model="addForm.VariantTypeID"  placeholder="请选择产品检测的变异类型" style="width: 280px">
              <Option v-for="(item,index) in VariantTypeArr" :value="item.VariantTypeID" :key="index">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="addForm.ProductDescription" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/product/index.php",qs.stringify({ProductID:this.productID})).then(response=>{
        // console.log(response.data);
        this.addForm.ProductName_cn = response.data.data[0].ProductName_cn;
      })
      this.$http.post("/php/datainterface/getVariantByType.php").then(response=>{
        // console.log(response.data);
        this.VariantTypeArr = response.data
      })
    },
    data() {
      const validateStartDate = (rule, value, callback) => {
        if(this.addForm.PanelCompleteDate){
          this.$refs.addForm.validateField('PanelCompleteDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.addForm.ProductOnlineDate){
          if(value<this.addForm.ProductOnlineDate){
            callback(new Error('结束日期不能早于启动日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        VariantTypeArr:[],
        addForm: {
          ProductID:location.search.split("=")[1],
          ProductName_cn:'',
          Version:'',
          ProductStatus:'',
          ProductOnlineDate:'',
          ProductOfflineDate:'',
          VariantTypeID:'',
          ProductDescription:''
        },
        rule:{
          ProductID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductOnlineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateStartDate, trigger: 'blur,change'}
          ],
          ProductOfflineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateEndDate, trigger: 'blur,change'}
          ],
          VariantTypeID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      changeLanchDate(date){
        this.addForm.ProductOnlineDate = date
      },
      changeCompleteDate(date){
        this.addForm.ProductOfflineDate = date
      },  
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj={}
            for(var key in this.addForm){
              obj[key] = this.addForm[key].trim()
            }
            this.$Common.add(this,"/php/product/productrecord/add.php",obj,()=>{
              this.$router.push("/browproduct?productid=" + this.addForm.ProductID)
            })
          }
        })
      },
      backList(){
        this.$router.push("/browproduct?productid=" + this.addForm.ProductID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


