<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">分子诊断管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的分子诊断</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的分子诊断</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的分子诊断</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="受检人编号" prop="SubjectID">
            <Input v-model="addForm.SubjectID" v-if="subjectID" disabled placeholder="请输入受检人编号" style="width: 280px"></Input>
            <AutoComplete v-model="addForm.SubjectID" v-if="!subjectID" @on-search="handleSearchID" @on-select="handleSelectID" placeholder="请输入受检人编号" style="width:280px">
              <Option v-for="(item,index) in subjectIDData" :value="item.SubjectID" :key="index">{{ item.SubjectID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="受检人姓名" prop="SubjectName">
            <Input v-model="addForm.SubjectName" v-if="subjectID" disabled placeholder="请输入受检人姓名" style="width: 280px"></Input>
            <AutoComplete v-model="addForm.SubjectName" v-if="!subjectID" @on-search="handleSearchName" @on-select="handleSelectName" placeholder="请输入受检人姓名" style="width:280px">
              <Option v-for="(item,index) in subjectNameData" :value="item.SubjectName" :key="index">{{ item.SubjectName }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="送检日期" prop="InspectionDate">
            <DatePicker type="date" @on-change="changeInspectionDate" show-week-numbers placeholder="请输入送检日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="送检医师" prop="Physician">
            <Input v-model="addForm.Physician" placeholder="请输入送检医师" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检机构" prop="InspectionInstitution">
            <Input v-model="addForm.InspectionInstitution" placeholder="请输入送检机构" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2>产品名称：</h2>
            <FormItem label="Panel名称">
              <Input v-model="addForm.PanelName_cn" disabled placeholder="请输入Panel名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product名称">
              <AutoComplete v-model="addForm.ProductName_cn" @on-search="searchProductName" @on-select="selectProductName" placeholder="请先输入产品中文名称" style="width:280px">
                <Option v-for="(item,index) in productData" :value="item.ProductName_cn" :key="index">{{item.ProductName_cn}}</Option>
              </AutoComplete>
            </FormItem>
            <FormItem label="Pipeline名称">
              <Input v-model="addForm.PipelineName_cn" disabled placeholder="请输入Pipeline名称" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="状态">
            <Select v-model="addForm.Status" placeholder="请选择状态" style="width:280px" placement="top">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <FormItem label="报告人">
            <Select v-model="addForm.ReporterID" placeholder="请输入报告人" style="width:280px" placement="top">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="审阅人">
            <Select v-model="addForm.ReviewerID" placeholder="请输入审阅人" style="width:280px" placement="top">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="报告日期">
            <DatePicker type="date" @on-change="changeReportDate" show-week-numbers placeholder="请输入报告日期" style="width: 280px" placement="top"></DatePicker>
          </FormItem>     
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      //获取受检人编号和姓名
      //判断网址中是否有参数
      if(this.subjectID===undefined){
        this.$http.post("/php/datainterface/getSubjectByName.php").then(response=>{
          // console.log(response.data);
          this.subjectIDArr = response.data
          this.subjectIDData = response.data
          this.subjectNameArr = response.data
          this.subjectNameData = response.data
        })
      }else{
        this.addForm.SubjectID = this.subjectID
        this.$http.post("/php/datainterface/getSubjectByName.php").then(response=>{
          // console.log(response.data);
          this.addForm.SubjectName = this.subjectID = response.data[0].SubjectName
        })
      }
      
      //获取product的ID和名称
      this.$http.post("/php/product/index.php",qs.stringify()).then(response=>{
        // console.log(response.data);
        this.productArr = response.data.data
        this.productData = response.data.data
      })
      //获取报告人和审阅人的编号和姓名
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
        // this.reviewerData = response.data
      })

    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
        subjectIDArr:[],
        subjectIDData:[],
        subjectNameArr:[],
        subjectNameData:[],
        productArr:[],
        productData:[],
        employeeData:[],
        addForm: {
          SubjectID:'',
          SubjectName:'',
          InspectionDate:'',
          Physician:'',
          InspectionInstitution:'',
          PanelName_cn:'',
          ProductID:'',
          ProductName_cn:'',
          PipelineName_cn:'',
          Status:'',//只能是数字
          ReporterID:'',
          ReviewerID:'',
          ReportDate:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          SubjectName:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          InspectionDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Physician:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          InspectionInstitution:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      changeInspectionDate(date){
        this.addForm.InspectionDate = date
      },
      changeReportDate(date){
        this.addForm.ReportDate = date
      },
      handleSearchID (value) {
        this.$http.post("/php/datainterface/getSubjectByName.php",qs.stringify({SubjectID:value})).then(response=>{
          // console.log(response.data);
          this.subjectIDArr = response.data
          this.subjectIDData = this.subjectIDArr.filter(item=>{
            return item.SubjectID.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      handleSelectID(value){
        this.addForm.SubjectID = value
        this.subjectIDData = this.subjectIDArr.filter(item=>{
          return item.SubjectID===value;
        })
        this.addForm.SubjectName = this.subjectIDData[0].SubjectName;
      },
      handleSearchName (value) {
        this.$http.post("/php/datainterface/getSubjectByNum.php",qs.stringify({Reporter:value})).then(response=>{
          // console.log(response.data);
          this.subjectNameArr = response.data
          this.subjectNameData = this.subjectNameArr.filter(item=>{
            return item.SubjectName.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      handleSelectName(value){
        this.addForm.SubjectName = value
        this.subjectNameData = this.subjectNameArr.filter(item=>{
          return item.SubjectName===value;
        })
        this.addForm.SubjectID = this.subjectNameData[0].SubjectID;
      },
      searchProductName(value){
        this.$http.post("/php/product/index.php",qs.stringify({ProductName_cn:value})).then(response=>{
          // console.log(response.data);
          this.productArr = response.data.data
          this.productData = response.data.data
        })
      },
      selectProductName(value){
        this.addForm.ProductName_cn = value
        this.productData = this.productArr.filter(item=>{
          return item.ProductName_cn===value;
        })
        this.addForm.ProductID = this.productData[0].ProductID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj={}
            for(var key in this.addForm){
              obj[key] = this.addForm[key].trim()
            }
            // console.log(obj);
            this.$Common.add(this,"/php/moldiagnosis/add.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


