<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/user">用户管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的用户</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的用户</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的用户</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="邮箱" prop="email">
            <Input v-model="addForm.email" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="LastName" prop="LastName">
            <Input v-model="addForm.LastName" placeholder="请输入LastName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="MiddleName">
            <Input v-model="addForm.MiddleName" placeholder="请输入MiddleName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="FirstName" prop="FirstName">
            <Input v-model="addForm.FirstName" placeholder="请输入FirstName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="权限" prop="authority">
            <Input v-model="addForm.authority" placeholder="请输入权限" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态" prop="status">
            <Input v-model="addForm.status" placeholder="请输入状态" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          email:'',
          LastName:'',
          MiddleName:'',
          FirstName:'',
          authority:'',
          status:''
        },
        rule:{
          email:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          LastName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          FirstName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          authority:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          status:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/user')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


