<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">信息分析流程管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browpipeline?pipelineid=' + this.pipelineID">查看信息分析流程</BreadcrumbItem>
      <BreadcrumbItem>添加新的信息分析流程升级记录</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的Panel升级记录</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的Panel升级记录</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="150">
          <FormItem label="信息分析流程编号" prop="PipelineProjectID">
            <Input v-model="addForm.PipelineProjectID" placeholder="请输入信息分析流程编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程版本号" prop="PipelineVersion">
            <Input v-model="addForm.PipelineVersion" placeholder="请输入信息分析流程版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程中文名称" prop="PipelineName_cn">
            <Input v-model="addForm.PipelineName_cn" placeholder="请输入信息分析流程中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程英文名称" prop="PipelineName_en">
            <Input v-model="addForm.PipelineName_en" placeholder="请输入信息分析流程英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="信息分析流程状态" prop="PipelineStatus">
            <Select v-model="addForm.PipelineStatus"  placeholder="请输入Panel状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="信息分析流程上线日期" prop="PipelineOnlineDate">
            <DatePicker @on-change="changeLanchDate" show-week-numbers placement="bottom-start" placeholder="请选择信息分析流程上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程下线日期" prop="PipelineOfflineDate">
            <DatePicker @on-change="changeCompleteDate" show-week-numbers placement="bottom-start" placeholder="请选择信息分析流程下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程描述">
            <Input type="textarea" v-model="addForm.PipelineDescription" placeholder="请输入信息分析流程描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post('/php/pipeline/list.php',qs.stringify({PipelineID:this.pipelineID})).then(response=>{
        // console.log(response.data);
        this.addForm.PipelineID = response.data.data[0].PipelineID
        this.addForm.PipelineProjectID = response.data.data[0].PipelineProjectID
      })
    },
    data() {
      const validateStartDate = (rule, value, callback) => {
        if(this.addForm.PipelineOfflineDate){
          this.$refs.addForm.validateField('PipelineOfflineDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.addForm.PipelineOnlineDate){
          if(value<this.addForm.PipelineOnlineDate){
            callback(new Error('下线日期不能早于上线日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        pipelineID:location.search.split('=')[1],
        addForm: {
          PipelineID:'',
          PipelineProjectID:'',
          PipelineVersion:'',
          PipelineName_cn:'',
          PipelineName_en:'',
          PipelineStatus:'',
          PipelineOnlineDate:'',
          PipelineOfflineDate:'',
          PipelineDescription:''
        },
        rule:{
          PipelineProjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PipelineVersion:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PipelineName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PipelineName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PipelineStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          PipelineOnlineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateStartDate, trigger: 'blur,change'}
          ],
          PipelineOfflineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateEndDate, trigger: 'blur,change'}            
          ]
        }
      }
    },
    methods: {
      changeLanchDate(date){
        this.addForm.PipelineOnlineDate = date
      },
      changeCompleteDate(date){
        this.addForm.PipelineOfflineDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              let obj = {}
              for(var key in this.addForm){
                obj[key] = this.addForm[key].trim()
              }
              // console.log(obj);
              this.$Common.add(this,"/php/pipeline/pipelinerecord/add.php",obj,()=>{
                this.$router.push("/browpipeline?pipelineid=" + this.addForm.PipelineID)
              })
            }
        })
      },
      backList(){
        this.$router.push("/browpipeline?pipelineid=" + this.addForm.PipelineID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


