<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>用户管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="邮箱">
            <Input v-model="searchForm.email" placeholder="请输入邮箱" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="权限">
            <Select v-model="searchForm.authority" placeholder="请选择权限" style="width: 162px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="searchForm.status" placeholder="请选择状态" style="width: 162px">
              <Option value="1111">启用</Option>
              <Option value="2222">禁用</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addUser">添加新用户</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          email:'',
          authority:'',
          status:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"创建日期",
            key:'samplingDate',
            align: 'center'
          },
          {
            title:"邮箱",
            key:'molDiagnosisID',
            align: 'center'
          },
          {
            title:"姓名",
            key:'bioID',
            align: 'center'
          },
          {
            title:"权限",
            key:'bioStyle',
            align: 'center'
          },
          {
            title:"状态",
            key:'samplingSite',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            molDiagnosisID:'111',
            bioID:'111',
            bioStyle:'111',
            samplingSite:'111',
            samplingDate:'111',
            collectionDate:'111',
            Sampler:'111',
            isCancerCell:'111',
            cancerCellPrecent:'111',
            pathologyID:'111',
            sampleMount:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addUser(){
        this.$router.push("/adduser")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browuser")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/edituser")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


