<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem :to="'/browproduct?productid='+ this.editForm.ProductID">查看产品</BreadcrumbItem>
      <BreadcrumbItem>编辑产品升级记录详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="productRecRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="editForm.ProductID" disabled placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品升级记录编号">
            <Input v-model="editForm.ProductUpdateRecordID" disabled placeholder="请输入产品升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="productName_cn" disabled placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号(0-99.99)" prop="Version">
            <Input v-model="editForm.Version" placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态" prop="ProductStatus">
            <Select v-model="editForm.ProductStatus"  placeholder="请选择产品状态" style="width: 280px">
              <Option value="Online">Online</Option>
              <Option value="Offline">Offline</Option>
            </Select>
          </FormItem>
          <FormItem label="产品上线日期" prop="ProductOnlineDate">
            <DatePicker @on-change="changeLanchDate" :value="editForm.ProductOnlineDate" placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期" prop="ProductOfflineDate">
            <DatePicker @on-change="changeCompleteDate" :value="editForm.ProductOfflineDate" placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型" prop="VariantTypeID">
            <Select v-model="editForm.VariantTypeID"  placeholder="请选择产品检测的变异类型" style="width: 280px">
              <Option v-for="(item,index) in VariantTypeArr" :value="item.VariantTypeID" :key="index">{{item.VariantType}}</Option>
            </Select>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="editForm.ProductDescription" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        this.$http.post("/php/product/productrecord/index.php",qs.stringify({ProductUpdateRecordID:this.productUpdateRecordID})).then(response=>{
          // console.log(response.data);
          this.editForm = response.data.data[0]
          resolve()
        })
      }).then(resolve=>{
        this.$http.post("/php/product/index.php",qs.stringify({ProductID:this.editForm.ProductID})).then(response=>{
          // console.log(response.data);
          this.productName_cn = response.data.data[0].ProductName_cn;
        })
        this.$http.post("/php/datainterface/getVariantByType.php").then(response=>{
          // console.log(response.data);
          this.VariantTypeArr = response.data
          this.editForm = this.VariantTypeArr.filter()
          
        })
      })
    },
    data() {
      const validateStartDate = (rule, value, callback) => {
        if(this.editForm.PanelCompleteDate){
          this.$refs.editForm.validateField('PanelCompleteDate');
        }
        callback()
      };
      const validateEndDate = (rule, value, callback) => {
        if(this.editForm.ProductOnlineDate){
          if(value<this.editForm.ProductOnlineDate){
            callback(new Error('结束日期不能早于启动日期！'))
          }else{
            callback()
          }
        }
      };
      return {
        productUpdateRecordID:location.search.split('=')[1],
        productName_cn:'',
        VariantTypeArr:[],
        editForm:{},
        rule:{
          ProductID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductUpdateRecordID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          Version:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductStatus:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          ProductOnlineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateStartDate, trigger: 'blur,change'}
          ],
          ProductOfflineDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            },
            {validator: validateEndDate, trigger: 'blur,change'}
          ],
          VariantTypeID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      productRecRemove(){
        this.$Common.remove(this,"/php/product/productrecord/del.php",{ProductUpdateRecordID:[this.productUpdateRecordID]},()=>{
          setTimeout(()=>{
            this.$router.push("/browproduct?productid=" + this.browForm.ProductID)
          },1000)
        })
      },
      changeLanchDate(date){
        this.editForm.ProductOnlineDate = date
      },
      changeCompleteDate(date){
        this.editForm.ProductOfflineDate = date
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            // console.log(this.editForm);
            let obj={}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            // console.log(obj);
            this.$Common.edit(this,'/php/product/productrecord/edit.php',obj,()=>{
              this.$router.push("/browproduct?productid=" + this.editForm.ProductID)
            })
          }
        })
      },
      backList(){
        this.$router.push("/browproduct?productid=" + this.editForm.ProductID)
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


