<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">分子诊断管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm"  :inline="true" :model="browForm" label-position="right" :label-width="120">
          <FormItem label="受检人编号" prop="SubjectID">
            <Input v-model="browForm.SubjectID" readonly placeholder="请输入受检人编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检人姓名" prop="SubjectName">
            <Input v-model="browForm.SubjectName" :readonly="flag" placeholder="请输入受检人姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子诊断编号" prop="MolDiagnosisID">
          <Input v-model="browForm.MolDiagnosisID" readonly placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检日期" prop="InspectionDate">
            <DatePicker type="date" :value="browForm.InspectionDate" readonly placeholder="请输入送检日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="送检医师" prop="Physician">
            <Input v-model="browForm.Physician" readonly placeholder="请输入送检医师" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检机构" prop="InspectionInstitution">
            <Input v-model="browForm.InspectionInstitution" readonly placeholder="请输入送检机构" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2>产品名称：</h2>
            <FormItem label="Panel名称">
              <Input v-model="browForm.PanelName_cn" readonly placeholder="请输入Panel名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product名称">
              <Input v-model="browForm.ProductName_cn" readonly placeholder="请先输入产品中文名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Pipeline名称">
              <Input v-model="browForm.PipelineName_cn" readonly placeholder="请输入Pipeline名称" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="状态">
            <Input v-model="browForm.Status" readonly placeholder="请输入状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告人">
            <Input v-model="browForm.ReporterName" readonly placeholder="请输入报告人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="审阅人">
            <Input v-model="browForm.ReviewerName" readonly placeholder="请输入审阅人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告日期">
            <DatePicker type="date" :value="browForm.InspectionDate" show-week-numbers readonly placeholder="请输入报告日期" style="width: 280px" placement="top"></DatePicker>
          </FormItem>    
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addBioSpeciemens">添加新样本</Button>
          </div>
          <div class="chromoListcontainer">
            <Table stripe border height="200" :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      new Promise((resolve,reject)=>{
        //获取报告人和审阅人的编号和姓名
        this.$Common.list(this,"/php/moldiagnosis/edit.php",{MolDiagnosisID:this.molDiagnosisID},(response)=>{
          this.browForm = response.data.data[0]
          this.handlePage(this.pageNum)
          setTimeout(()=>{resolve()})
        })
        
      }).then(()=>{
        this.$http.post("/php/datainterface/getSubjectByName.php").then(response=>{
          // console.log(response.data);
          this.browForm.SubjectName = response.data.filter(item=>{
            return item.SubjectID===this.browForm.SubjectID
          })[0].SubjectName
          //防止受检人姓名无法显示，因为readonly属性一旦触发会导致值无法传入
          this.flag = true;
        })
      })
    },
    data() {
      return {
        molDiagnosisID:location.search.split("=")[1],
        flag:false,
        browForm:{},
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"样本编号",
            key:'BiospeciemensID',
            align: 'center'
          },
          {
            title:"病理编号",
            key:'PathologicNumber',
            align: 'center'
          },
          {
            title:"样本原始编号",
            key:'SampleOriID',
            align: 'center'
          },
          {
            title:"样本公司编号",
            key:'SampleSGIID',
            align: 'center'
          },
          {
            title:"样本类型",
            key:'SampleType',
            align: 'center'
          },
          {
            title:"取样部位",
            key:'Site',
            align: 'center'
          },
          {
            title:"取样日期",
            key:'SamplingDate',
            align: 'center'
          },
          {
            title:"收样日期",
            key:'ReceivedDate',
            align: 'center'
          },
          {
            title:"收样人",
            key:'Name',
            align: 'center'
          },
          {
            title:"是否为肿瘤细胞",
            key:'IsTumorCell',
            align: 'center'
          },
          {
            title:"肿瘤细胞含量",
            key:'TumorCellContent',
            align: 'center'
          },
          {
            title:"样本量",
            key:'Quantity',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.BiospeciemensID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.BiospeciemensID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.BiospeciemensID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editmoldiagnosis?moldiagnosisid=" + this.molDiagnosisID)
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      },
      addBioSpeciemens(){
        this.$router.push("/addbiospeciemens?moldiagnosisid=" + this.molDiagnosisID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browbiospeciemens?biospeciemensid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editbiospeciemens?biospeciemensid=" + id)
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {
          MolDiagnosisID:this.molDiagnosisID,
          pageNum:this.pageNum,
          pageSize:this.pageSize
        }
        this.$Common.list(this,"/php/biospecimens/index.php",obj,(response)=>{
          this.tableData = response.data.data
          this.TotalNum = Number(response.data.num)
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .chromoListcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:5px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding: 10px;
      }
    }
  }
}
</style>


