<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="libRemove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="140">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库编号" prop="LibID">
            <Input v-model="editForm.LibID" disabled placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸编号" prop="NucleicAcidID">
            <Input v-model="editForm.NucleicAcidID" placeholder="请输入核酸编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="LibSGIID">
            <Input v-model="editForm.LibSGIID" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建人"  prop="LibConsManipulatorID">
            <Select v-model="editForm.LibConsManipulatorID" placeholder="请输入文库构建人" style="width:280px">
              <Option v-for="(item,index) in employeeData" :value="item.EmployeeID" :key="index">{{ item.Fullname }}</Option>
            </Select>
          </FormItem>
          <FormItem label="文库构建日期" prop="LibConsDate">
            <DatePicker type="date" @on-change="changeLibConsDate" :value="editForm.LibConsDate" show-week-numbers placement="bottom-start" placeholder="请选择文库构建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="editForm.LibConsAmount" placeholder="请输入文库构建用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量单位">
            <Input v-model="editForm.LibConsUnit" placeholder="请输入文库构建用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积">
              <Input v-model="editForm.LibVolume" placeholder="请输入文库体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积单位">
              <Input v-model="editForm.LibVolumeUnit" placeholder="请输入文库体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度">
              <Input v-model="editForm.LibConc" placeholder="请选择文库浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度单位">
              <Input v-model="editForm.LibConcUnit" placeholder="请选择文库浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量">
            <Input v-model="editForm.LibAmount" placeholder="请输入文库总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量单位">
            <Input v-model="editForm.LibAmountUnit" placeholder="请输入文库总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小">
            <Input v-model="editForm.LibInsSize" placeholder="请输入文库插入片段大小" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小单位">
            <Input v-model="editForm.LibInsSizeUnit" placeholder="请输入文库插入片段大小单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库质量">
            <Select v-model="editForm.LibQuality" placeholder="请选择文库质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="差">差</Option>
            </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.Status" placeholder="请选择状态" style="width: 280px">
              <Option value="1">状态1</Option>
              <Option value="2">状态2</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/datainterface/getEmployeeName.php").then(response=>{
        // console.log(response.data);
        this.employeeData = response.data
      })
      this.$Common.list(this,"/php/library/edit.php",{LibID:this.libID},(response)=>{
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        libID:location.search.split("=")[1],
        employeeData:[],
        editForm:{},
        rule:{
          NucleicAcidID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibSGIID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibConsManipulatorID:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ],
          LibConsDate:[
            {
              required: true, message: '不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      libRemove(){
        this.$Common.remove(this,"/php/library/del.php",{LibID:[this.libID]},()=>{
          setTimeout(()=>{
            history.back();
          },1000)
        })
      },
      changeLibConsDate(date){
        this.editForm.LibConsDate = date
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            for(var key in this.editForm){
              if(key!=='CreateDate'&&key!=='LatestUpdateDate'){
                obj[key] = this.editForm[key].trim()
              }
            }
            console.log(obj);
            this.$Common.edit(this,"/php/library/update.php",obj,()=>{
              // this.$router.push(this.$store.state.basic.backUrl)
              history.back()
            })
          }
        })
      },
      backList(){
        // this.$router.push(this.$store.state.basic.backUrl)
        history.back()
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


